// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import VueResource from 'vue-resource'
import fastclick from 'fastclick'
import store from './store/index.js'
import VueI18n from 'vue-i18n'
import log from './assets/js/log.js'
import jsInjection from './assets/js/jsInjectionj.js'

import './assets/css/common.css'

Vue.config.productionTip = false
Vue.use(VueResource)
Vue.use(VueI18n)

log.debug('返回的初始语言： ' + jsInjection.get('locale'))
// i-18n
const messages = {
    'am_ET': require('./i18n/lang/am_ET'),
    'ar_SA': require('./i18n/lang/ar_SA'),
    'as_IN': require('./i18n/lang/as_IN'),
    'az_AZ': require('./i18n/lang/az_AZ'),
    'be_BY': require('./i18n/lang/be_BY'),
    'bg_BG': require('./i18n/lang/bg_BG'),
    'bn_BD': require('./i18n/lang/bn_BD'),
    'bo_CN': require('./i18n/lang/bo_CN'),
    'bs_BA': require('./i18n/lang/bs_BA'),
    'ca_AD': require('./i18n/lang/ca_AD'),
    'cs_CZ': require('./i18n/lang/cs_CZ'),
    'da_DK': require('./i18n/lang/da_DK'),
    'de_DE': require('./i18n/lang/de_DE'),
    'el_GR': require('./i18n/lang/el_GR'),
    'en_GB': require('./i18n/lang/en_GB'),
    'en_US': require('./i18n/lang/en_US'),
    'es_ES': require('./i18n/lang/es_ES'),
    'es_US': require('./i18n/lang/es_US'),
    'et_EE': require('./i18n/lang/et_EE'),
    'eu_ES': require('./i18n/lang/eu_ES'),
    'fa_IR': require('./i18n/lang/fa_IR'),
    'fi_FI': require('./i18n/lang/fi_FI'),
    'fr_FR': require('./i18n/lang/fr_FR'),
    'gl_ES': require('./i18n/lang/gl_ES'),
    'gu_IN': require('./i18n/lang/gu_IN'),
    'hi_IN': require('./i18n/lang/hi_IN'),
    'hr_HR': require('./i18n/lang/hr_HR'),
    'hu_HU': require('./i18n/lang/hu_HU'),
    'in_ID': require('./i18n/lang/in_ID'),
    'it_IT': require('./i18n/lang/it_IT'),
    'he_IL': require('./i18n/lang/he_IL'),
    'iw_IL': require('./i18n/lang/he_IL'),
    'ja_JP': require('./i18n/lang/ja_JP'),
    'jv': require('./i18n/lang/jv'),
    'ka_GE': require('./i18n/lang/ka_GE'),
    'kk_KZ': require('./i18n/lang/kk_KZ'),
    'km_KH': require('./i18n/lang/km_KH'),
    'kn_IN': require('./i18n/lang/kn_IN'),
    'ko_KR': require('./i18n/lang/ko_KR'),
    'lo_LA': require('./i18n/lang/lo_LA'),
    'lt_LT': require('./i18n/lang/lt_LT'),
    'lv_LV': require('./i18n/lang/lv_LV'),
    'mai_Deva_IN': require('./i18n/lang/mai_Deva_IN'),
    'mi_NZ': require('./i18n/lang/mi_NZ'),
    'mk_MK': require('./i18n/lang/mk_MK'),
    'ml_IN': require('./i18n/lang/ml_IN'),
    'mn_MN': require('./i18n/lang/mn_MN'),
    'mr_IN': require('./i18n/lang/mr_IN'),
    'ms_MY': require('./i18n/lang/ms_MY'),
    'my_MM': require('./i18n/lang/my_MM'),
    'my_zz': require('./i18n/lang/my_zz'),
    'nb_NO': require('./i18n/lang/nb_NO'),
    'ne_NP': require('./i18n/lang/ne_NP'),
    'nl_NL': require('./i18n/lang/nl_NL'),
    'or_IN': require('./i18n/lang/or_IN'),
    'pa_Guru_IN': require('./i18n/lang/pa_Guru_IN'),
    'pl_PL': require('./i18n/lang/pl_PL'),
    'pt_BR': require('./i18n/lang/pt_BR'),
    'pt_PT': require('./i18n/lang/pt_PT'),
    'ro_RO': require('./i18n/lang/ro_RO'),
    'ru_RU': require('./i18n/lang/ru_RU'),
    'si_LK': require('./i18n/lang/si_LK'),
    'sk_SK': require('./i18n/lang/sk_SK'),
    'sl_SI': require('./i18n/lang/sl_SI'),
    'sr_MS': require('./i18n/lang/sr_MS'),
    'sr_RS': require('./i18n/lang/sr_RS'),
    'sv_SE': require('./i18n/lang/sv_SE'),
    'sw_TZ': require('./i18n/lang/sw_TZ'),
    'ta_IN': require('./i18n/lang/ta_IN'),
    'te_IN': require('./i18n/lang/te_IN'),
    'th_TH': require('./i18n/lang/th_TH'),
    'tl_PH': require('./i18n/lang/tl_PH'),
    'tr_TR': require('./i18n/lang/tr_TR'),
    'uk_UA': require('./i18n/lang/uk_UA'),
    'ur_PK': require('./i18n/lang/ur_PK'),
    'vi_VN': require('./i18n/lang/vi_VN'),
    'zh_CN': require('./i18n/lang/zh_CN'),
    'zh_HK': require('./i18n/lang/zh_HK'),
    'zh_TW': require('./i18n/lang/zh_TW'),
    'uz_UZ': require('./i18n/lang/uz_UZ'),
    'ug_CN': require('./i18n/lang/ug_CN')
}
const defaultLocale = 'en_US'
var locale = jsInjection.get('locale') // 语言标识
var localeFounded = false
for (var prop in messages) {
    if (locale === prop) {
        localeFounded = true
        break
    }
}
const defaultLanguageConfig = {
    'en': 'en_US',
    'es': 'es_ES',
    'pt': 'pt_BR',
    'sr': 'sr_MS'
}
// 只根据语言进行匹配
if (!localeFounded) {
    var language = locale.split('_')[0]
    // 遍历以上4种存在默认语言的语种
    for (prop in defaultLanguageConfig) {
        if (prop === language) {
            locale = defaultLanguageConfig[prop]
            localeFounded = true
            break
        }
    }
    // 从messages中遍历查找语言
    for (prop in messages) {
        if (localeFounded) {
            break
        }
        var configLanguage = prop.split('_')[0]
        if (configLanguage === language) {
            locale = prop
            localeFounded = true
        }
    }
}
// 仍然没有找到匹配的语言，使用默认语言en_US
if (!localeFounded) {
    locale = defaultLocale
}
log.info('locale = ' + locale)
const i18n = new VueI18n({
    locale: locale,
    messages: messages
})
// 移动端点击优化
fastclick.attach(document.body)

/* eslint-disable no-new */
var app = new Vue({
    el: '#app',
    i18n,
    router,
    store,
    components: { App },
    template: '<App/>'
})
export default app



