<template>
    <div class='dial'>
        <img class='dialImg' :src='currentSrc' v-show="hasImg">
        <div class='dialImgGray' v-show="!hasImg"></div>
        <p class='dialName font_12'>{{dialList["title-local"]}}</p>
    </div>
</template>

<script>
import log from '../../assets/js/log.js'
import config from '../../assets/js/config.js'
import {mapGetters} from 'vuex'
export default{
    props: {
        'dialList': {
            type: Object,
            default: null
        },
        'fileHost': {
            type: String,
            default: ''
        }
    },
    data () {
        return {
            active: true,
            hasImg: true
        }
    },
    beforeUpdate () {
        this.hasImg = this.dialList.previews !== ''
        var imgList = document.getElementsByClassName('dialImg')
        for (var i in imgList) {
            if (imgList[i].offsetWidth !== imgList[i].offsetHeight) {
                imgList[i].height = imgList[i].offsetWidth
            }
        }
    },
    activated () {
        this.hasImg = this.dialList.previews !== ''
    },
    computed: {
        ...mapGetters([
            'isMiddleEastLanguage',
            'currentDownloadWatchFaceId',
            'apiSign',
            'defaultWatchId'
        ]),
        currentSrc () {
            if (this.dialList.pushNum === config.WATCH_CASSINI) {
                log.info(config.WATCH_FACE_PATHPREFIX + this.dialList.previews)
                return (config.WATCH_FACE_PATHPREFIX + this.dialList.previews)
            } else {
                let previews = this.dialList.previews.split('|').filter((value, index, array) => {
                    if (value === config.ICON_SMALL_JPG || value === config.ICON_SMALL_PNG) {
                        return true
                    }
                    return false
                })
                let iconSrc = (previews.length > 0) ? previews[0] : this.dialList.logo
                log.info('watchFaceIcon: ' + this.fileHost + this.dialList.id + '/' + iconSrc)
                return (this.fileHost + this.dialList.id + '/' + iconSrc)
            }
        }
    }
}
</script>

<style lang="scss" type="stylesheet/scss">
.recomDialListClass{
    width: 100%;
    height: 100%;
    font-size:0;
    .dialImg{
        width: 100%;
        border-radius: 50%;
        background: #f2f2f2;
    }
    .dialImgGray{
        width: 100%;
        height: 13.95rem;
        border-radius: 6.975rem;
        background-color: #f2f2f2;
    }
    .dialName{
      width: 100%;
      line-height: 1.5rem;
      text-align: center;
      margin: 0.4rem auto 0.4rem;
      color: rgba(0,0,0,0.5);
      overflow: hidden;
    }
@media (max-width: 320px){
  .dialImg{
    width: 100%;
    border-radius: 50%;
    background: #f2f2f2;
  }
  .dialImgGray{
    width: 100%;
    height: 12.21rem;
    border-radius: 6.105rem;
    background-color: #f2f2f2;
  }
}
.dialName{
        width: 100%;
        text-align: center;
        margin: 0.4rem auto 0.4rem;
        color: rgba(0,0,0,0.5);
        overflow: hidden;
    }
}
.myDialListClass{
  width: 100%;
  height: 100%;
  font-size:0;
  color:rgba(0,0,0,0.5);
  .dialImg{
      width: 8.8rem;
      height: 8.8rem;
      border-radius: 4.4rem;
      margin-left: 0.8rem;

      margin-top: 0.8rem;
  }
  .dialImgGray {
      width: 8.8rem;
      height: 8.8rem;
      border-radius: 4.4rem;
      margin-left: 0.8rem;
      margin-top: 0.8rem;
      background-color:#f2f2f2;
  }
  .dialName{
    width: 100%;
    text-align: center;
    margin: 0.4rem auto 0.8rem;
    padding:0;
    overflow: hidden;
  }
  .dialNameActiveColor{
    color:#1A1A1A;
  }
  .dialNameColor{
    color:rgba(0,0,0,0.5);
  }
  @media (max-width: 320px){
    .dialImg{
      width: 7.72rem;
      height: 7.72rem;
      border-radius: 3.86rem;
      margin-left: 0.6rem;
      margin-top: 0.6rem;
    }
    .dialImgGray {
      width: 7.72rem;
      height: 7.72rem;
      border-radius: 3.86rem;
      margin-left: 0.6rem;
      margin-top: 0.6rem;
      background-color:#f2f2f2;
    }
  }
}
</style>



