<template>
    <ul class="dialUl">
        <li  v-for='(item, index) in dataMyList' v-if='dataMyList[index].watchThemeModle !== 2' class='myDialList' :class="myDialItemClass" :key='item.index'>
            <router-link :to="{path:'/watchThemeDetail',query:{data:dataMyList[index],fileHost:fileHost,showSizeCount:false}}">
                <v-singleDial class='myDialListClass' :class='{default:dataMyList[index].watchThemeModle === 0}' :dialList ='dataMyList[index]' :fileHost= 'fileHost'></v-singleDial>
            </router-link>
        </li>
    </ul>
</template>

<script>
import singleDial from '@/components/common/singleDial'
import {mapGetters} from 'vuex'
export default {
    name: 'index',
    props: {
        'dataMyList': {
            type: Array,
            default: null
        },
        'fileHost': {
            type: String,
            default: ''
        }
    },
    components: {
        'v-singleDial': singleDial
    },
    computed: {
        ...mapGetters([
            'isMiddleEastLanguage',
            'isBigCD'
        ])
    },
    data () {
        return {
            myDialItemClass: []
        }
    },
    mounted () {
        // 适配大溪地样式
        this.computeMyDialItemStyle()
    },
    methods: {
        computeMyDialItemStyle () {
            let myDialItemClazz = []
            if (this.isBigCD) {
                if (this.isMiddleEastLanguage) {
                    myDialItemClazz.push('myDialListBigCDReversal')
                } else {
                    myDialItemClazz.push('myDialListBigCDNormal')
                }
            } else {
                if (this.isMiddleEastLanguage) {
                    myDialItemClazz.push('myDialListReversal')
                } else {
                    myDialItemClazz.push('myDialListNormal')
                }
            }
            this.myDialItemClass = myDialItemClazz
        }
    }
}
</script>

<style lang="scss" type="stylesheet/scss">
.dialUl{
    .myDialList{
        position: relative;
        width: 10.4rem;
        height: 12.25rem;
    }
    .myDialList:active{
      background:#f2f2f2;
      border-radius:0.4rem;
    }
    .myDialListNormal{
        float: left;
    }
    .myDialListReversal{
        float: right;
    }
    .myDialListNormal:nth-child(3n+1){
        margin: 0.8rem 0.8rem 0.8rem 1.6rem
    }
    .myDialListNormal:nth-child(3n+2){
        margin: 0.8rem 0.8rem 0.8rem 0rem
    }
    .myDialListNormal:nth-child(3n+3){
        margin: 0.8rem 1.6rem 0.8rem 0rem
    }
    .default{
        border-radius: 0.4rem;
        background-color: #ffe8de;
        color:#1A1A1A !important;
    }
    .myDialListReversal:nth-child(3n+1){
        margin: 0.8rem 1.6rem 0.8rem 0.8rem
    }
    .myDialListReversal:nth-child(3n+2){
        margin: 0.8rem 0rem 0.8rem 0.8rem
    }
    .myDialListReversal:nth-child(3n+3){
        margin: 0.8rem 0rem 0.8rem 1.6rem
    }
    .myDialListBigCDNormal{
        float: left;
    }
    .myDialListBigCDReversal{
        float: right;
    }
    .myDialListBigCDNormal:nth-child(6n+1){
      margin: 0.6rem 0.64rem 0.6rem 1.6rem
    }
    .myDialListBigCDNormal:nth-child(6n+2){
      margin: 0.6rem 0.64rem 0.6rem 0.64rem
    }
    .myDialListBigCDNormal:nth-child(6n+3){
      margin: 0.6rem 0.64rem 0.6rem 0.64rem
    }
    .myDialListBigCDNormal:nth-child(6n+4){
      margin: 0.6rem 0.64rem 0.6rem 0.64rem
    }
    .myDialListBigCDNormal:nth-child(6n+5){
      margin: 0.6rem 0.64rem 0.6rem 0.64rem
    }
    .myDialListBigCDNormal:nth-child(6n+6){
      margin: 0.6rem 1.6rem 0.6rem 0.64rem
    }
    .myDialListBigCDReversal:nth-child(6n+1){
      margin: 0.6rem 1.6rem 0.6rem 0.64rem
    }
    .myDialListBigCDReversal:nth-child(6n+2){
      margin: 0.6rem 0.64rem 0.6rem 0.64rem
    }
    .myDialListBigCDReversal:nth-child(6n+3){
      margin: 0.6rem 0.64rem 0.6rem 0.64rem
    }
    .myDialListBigCDReversal:nth-child(6n+4){
      margin: 0.6rem 0.64rem 0.6rem 0.64rem
    }
    .myDialListBigCDReversal:nth-child(6n+5){
      margin: 0.6rem 0.64rem 0.6rem 0.64rem
    }
    .myDialListBigCDReversal:nth-child(6n+6){
      margin: 0.6rem 0.64rem 0.6rem 1.6rem
    }
  @media (max-width: 320px) {
    .myDialList{
      position: relative;
      width: 9rem;
      height: 10.76rem;
    }
    .myDialListNormal:nth-child(3n+1){
      margin: 0.6rem 0.6rem 0.6rem 1.2rem
    }
    .myDialListNormal:nth-child(3n+2){
      margin: 0.6rem 0.6rem 0.6rem 0rem
    }
    .myDialListNormal:nth-child(3n+3){
      margin: 0.6rem 1.2rem 0.6rem 0rem
    }
  }
}
</style>



