import Vue from 'vue'
import log from './log.js'
import jsInjection from './jsInjectionj.js'
import config from './config.js'
const vm = new Vue()
const DEFAULT_VERSION_CODE = 80003 // 版本号大于70150后，返回的图片路径为https,值取大于70150的任意值
export default{
    // mpcPortal 请求路径的后缀
    // parmers 请求的参数
    // success 成功回调函数
    // error 失败回调函数
    request (mpcPortal, params, success, error) {
        var watchFaceInfo = jsInjection.get('watchFaceInfo')
        try {
            watchFaceInfo = JSON.parse(watchFaceInfo)
        } catch (e) {
            log.info('native param is null')
        }
        var urlBase = jsInjection.get('urlBase')
        log.info('urlBase: ' + urlBase)
        // 路径与接口映射
        var action = {
            getSize: 'login-client.do',
            queryAllDial: 'gettheme.do',
            downloadFeedback: 'downloadFeedback.do'
        }
        if (action[mpcPortal]) {
            urlBase = urlBase + action[mpcPortal]
        }
        log.info('urlBase: ' + urlBase)
        var firmware = watchFaceInfo.firmware
        var screen = watchFaceInfo.screen
        var phoneType = watchFaceInfo.phoneType
        var buildNumber = watchFaceInfo.buildNumber
        var watchThemeVersion = watchFaceInfo.themeVersion
        var locale = watchFaceInfo.locale
        var isoCode = watchFaceInfo.isoCode
        // 判断请求方式
        var requestType = 0

        var requestPost = ['getSize']
        var requestGet = ['queryAllDial', 'downloadFeedback']
        log.info(requestGet)

        // 设置post公共参数
        var defaultParamPost = {
            firmware: firmware,
            screen: screen,
            phoneType: phoneType,
            buildNumber: buildNumber,
            locale: locale,
            isoCode: isoCode,
            encryptType: 0
        }
        log.info('defaultParamPost: ' + JSON.stringify(defaultParamPost))
        var defaultParamGet = {
            themeVersion: watchThemeVersion,
            versionCode: DEFAULT_VERSION_CODE,
            filetype: 'hwt'
        }
        log.info('defaultParamGet: ' + JSON.stringify(defaultParamGet))
        if (requestPost.indexOf(mpcPortal) > -1) {
            requestType = 1
            params = Object.assign(defaultParamPost, params)
        } else {
            requestType = 2
            params = Object.assign(defaultParamGet, params)
        }
        var optionsPost = {
            emulateJSON: true
        }
        log.info('url is: ' + urlBase + ' params: ' + JSON.stringify(params))
        var startTime = Date.now()
        var endTime
        log.info(mpcPortal + ' start : ' + Date.now())
        if (requestType === 1) {
            vm.$http.post(urlBase, params, optionsPost).then(data => {
                log.debug('dealReceive ' + JSON.stringify(data))
                endTime = Date.now()
                log.info(mpcPortal + ' end : ' + Date.now() + '')
                log.info('api duration# success: ' + urlBase + mpcPortal + ', ' + (endTime - startTime))
                startTime = 0
                endTime = 0
                // 判断返回码异常情况
                if (success) {
                    if (data.body.resultcode === '0' && data.body.resultinfo === 'success') {
                        success(data.body)
                    } else {
                        log.info('request failed! code: ' + data.body.resultCode + '; info: ' + data.body.resultinfo)
                        jsInjection.sendServerErrMsg()
                    }
                }
            }, data => {
                endTime = Date.now()
                log.info(mpcPortal + ' endFail : ' + Date.now())
                log.info('请求失败： ' + JSON.stringify(data))
                log.info('api duration# fail: ' + urlBase + mpcPortal + ', ' + (endTime - startTime))
                startTime = 0
                endTime = 0
                // 判断是否有网
                if (!jsInjection.get('isNetworkConnected')) {
                    log.info('isNetworkConnected is false')
                    jsInjection.sendNoNetMsg()
                    return
                }
                if (error) {
                    error(data)
                } else {
                    jsInjection.sendServerErrMsg()
                    log.info('request fail!')
                }
            })
        } else {
            var optionsGet = {
                url: '',
                method: 'get',
                params: params,
                timeout: 1e4
            }
            vm.$http.get(urlBase, optionsGet).then(data => {
                // 根据判断码判断
                log.debug('dealReceive ： ' + JSON.stringify(data))
                log.info(mpcPortal + ' end : ' + Date.now())
                if (success) {
                    if (data && data.body) {
                        if (data.body.resultcode === config.API_ERRCODE_PARAMS_ERR) {
                            log.info('request failed! code: ' + data.body.resultCode + '; info: ' + data.body.resultinfo)
                            jsInjection.sendServerErrMsg()
                        } else if (data.body.resultcode === config.API_ERRCODE_SERVER_ERR) {
                            log.info('request failed! code: ' + data.body.resultCode + '; info: ' + data.body.resultinfo)
                            jsInjection.sendServerErrMsg()
                        } else if (data.body.resultcode === config.API_ERRCODE_NODATA_ERR) {
                            log.info('request failed! code: ' + data.body.resultCode + '; info: ' + data.body.resultinfo)
                            jsInjection.sendServerErrMsg()
                        } else {
                            if (localStorage.getItem('retryTimes') != null) {
                                log.info('remove retryTimes otherCloud#')
                                localStorage.removeItem('retryTimes')
                            }
                            success(data.body)
                        }
                    }
                }
            }, data => {
                log.info(mpcPortal + ' endFail : ' + Date.now())
                log.info('请求失败： ' + JSON.stringify(data))
                // 判断是否有网
                if (!jsInjection.get('isNetworkConnected')) {
                    log.info('isNetworkConnected is false')
                    jsInjection.sendNoNetMsg()
                    return
                }
                if (error) {
                    error(data)
                } else {
                    jsInjection.sendServerErrMsg()
                    log.info('request fail!')
                }
            })
        }
    }
}



