import log from './log.js'

// BI key值
const CLICK_WATCH_FACE_BI = 2180001 // 进入表盘市场
const INSTALL_AND_UPDATE_SUCCESS_BI = 2180002 // 安装和更新成功
const INSTALL_AND_UPDATE_FAIL_BI = 2180003 // 安装和更新失败
const SET_CURRECT_WATCH_FACE_BI = 2180004 // 设为当前表盘

// 运动健康统计
function setBIEvent (key, mapJson, level) {
    log.info('BIEvent# key = ' + key + ',setBIEvent = ' + mapJson)
    if (!window.JsInteraction) {
        return
    }
    window.JsInteraction.setBIEvent(key, mapJson, level)
}

export default {
    // 进入表盘市场，显示表盘市场是否成功
    clickWatchFace (type) {
        var mapJson = {
            showSuccess: type
        }
        var mapJsonStr = JSON.stringify(mapJson)
        setBIEvent(CLICK_WATCH_FACE_BI, mapJsonStr, 0)
    },
    // 安装和更新成功的时长
    installAndUpdateSuccess (hitopid, version, duration) {
        var mapJson = {
            hitopid: hitopid,
            version: version,
            duration: duration
        }
        var mapJsonStr = JSON.stringify(mapJson)
        setBIEvent(INSTALL_AND_UPDATE_SUCCESS_BI, mapJsonStr, 0)
    },
    //  安装和更新失败的原因
    installAndUpdateFail (hitopid, version, errorType) {
        var mapJson = {
            hitopid: hitopid,
            version: version,
            errorType: errorType
        }
        var mapJsonStr = JSON.stringify(mapJson)
        setBIEvent(INSTALL_AND_UPDATE_FAIL_BI, mapJsonStr, 0)
    },
    // 设为当前表盘失败和成功次数
    setCurrentWatchFace (hitopid, version, status) {
        var mapJson = {
            hitopid: hitopid,
            version: version,
            status: status
        }
        var mapJsonStr = JSON.stringify(mapJson)
        setBIEvent(SET_CURRECT_WATCH_FACE_BI, mapJsonStr, 0)
    }
}



