<template>
    <div id="app">
      <keep-alive v-bind:exclude="['watchThemeDetail']">
        <router-view v-if="isRouterAlive"></router-view>
      </keep-alive>
    </div>
</template>

<script>
export default {
    name: 'App',
    provide () {
        return {
            reload: this.reload
        }
    },
    data () {
        return {
            isRouterAlive: true
        }
    },
    created () {
        window.transmitProgress = this.transmitProgress
        // 设置默认表盘结果
        window.transmitSetDefaultWatchFaceResult = this.transmitSetDefaultWatchFaceResult
        // 取消安装结果
        window.transmitCancelInstallWatchFaceResult = this.transmitCancelInstallWatchFaceResult
        // 删除表盘结果
        window.transmitDeleteWatchFaceResult = this.transmitDeleteWatchFaceResult
        // 安装中断异常结果
        window.transmitInstallWatchFaceResult = this.transmitInstallWatchFaceResult
        window.defaultWatchChanged = this.defaultWatchChanged
        // 下载进度
        window.transmitDownloadProgressWatchFace = this.transmitDownloadProgressWatchFace
        // 下载结果
        window.transmitDownloadWatchFaceResult = this.transmitDownloadWatchFaceResult
        // 穿戴设备是否连接
        window.transmiDeviceConnectState = this.transmiDeviceConnectState
        document.body.addEventListener('touchstart', function () { })
    },
    methods: {
        transmitProgress (hitopid, currentProgress) {
            var event = document.createEvent('HTMLEvents')
            event.initEvent('transmitProgress', false, false)
            event.data = {
                'hitopid': hitopid,
                'currentProgress': currentProgress
            }
            window.isDownloadingWatchFace = true
            document.dispatchEvent(event)
        },
        transmitSetDefaultWatchFaceResult (hitopid, version, result) {
            var event = document.createEvent('HTMLEvents')
            event.initEvent('transmitSetDefaultWatchFaceResult', false, false)
            event.data = {
                'hitopid': hitopid,
                'version': version,
                'result': result
            }
            document.dispatchEvent(event)
        },
        transmitCancelInstallWatchFaceResult (hitopid, version, result) {
            var event = document.createEvent('HTMLEvents')
            event.initEvent('transmitCancelInstallWatchFaceResult', false, false)
            event.data = {
                'hitopid': hitopid,
                'version': version,
                'result': result
            }
            document.dispatchEvent(event)
        },
        transmitDeleteWatchFaceResult (hitopid, version, result) {
            var event = document.createEvent('HTMLEvents')
            event.initEvent('transmitDeleteWatchFaceResult', false, false)
            event.data = {
                'hitopid': hitopid,
                'version': version,
                'result': result
            }
            document.dispatchEvent(event)
        },
        transmitInstallWatchFaceResult (hitopid, version, result) {
            var event = document.createEvent('HTMLEvents')
            event.initEvent('transmitInstallWatchFaceResult', false, false)
            event.data = {
                'hitopid': hitopid,
                'version': version,
                'result': result
            }
            document.dispatchEvent(event)
        },
        defaultWatchChanged () {
            var event = document.createEvent('HTMLEvents')
            event.initEvent('defaultWatchChanged', false, false)
            document.dispatchEvent(event)
        },
        transmitDownloadProgressWatchFace (hitopid, downloadProgress) {
            var event = document.createEvent('HTMLEvents')
            event.initEvent('downloadWatchFaceProgress', false, false)
            event.data = {
                'hitopid': hitopid,
                'downloadProgress': downloadProgress
            }
            window.isDownloadingWatchFace = true
            document.dispatchEvent(event)
        },
        transmitDownloadWatchFaceResult (hitopid, version, resultCode) {
            var event = document.createEvent('HTMLEvents')
            event.initEvent('downloadWatchFaceResult', false, false)
            event.data = {
                'hitopid': hitopid,
                'version': version,
                'resultCode': resultCode
            }
            document.dispatchEvent(event)
        },
        transmiDeviceConnectState (isDeviceConnected, tipsText) {
            var event = document.createEvent('HTMLEvents')
            event.initEvent('setTipsBarStatusChanged', false, false)
            event.data = {
                'isDeviceConnected': isDeviceConnected,
                'tipsText': tipsText
            }
            document.dispatchEvent(event)
        },
        reload () {
            this.isRouterAlive = false
            this.$nextTick(() => {
                this.isRouterAlive = true
            })
        }
    }
}
</script>

<style>
#app {

}
</style>



