import log from './log.js'
import config from './config'

var actions = {
    urlBase: 'getWatchFaceUrlBase',
    phoneWatchType: 'getPhoneWatchType',
    screenSize: 'getScreenSize',
    firmware: 'getFirmware',
    buildNumber: 'getBuildNumber',
    watchThemeVersion: 'getWatchThemeVersion',
    watchFileType: 'getWatchFileType',
    currentWatchFaceId: 'getCurrentWatchFaceId',
    watchFaceIdStore: 'getWatchFaceIdStore',
    preWatchFaceIdStore: 'getPreWatchFaceIdStore',
    locale: 'getLocale',
    isoCode: 'getIsoCode ',
    watchFaceInfo: 'getWatchFaceInfo',
    isNetworkConnected: 'isNetworkConnected',
    getWatchFaceInstallState: 'getWatchFaceInstallState',
    getBigCDStatus: 'isBigCD'
}

export default {

    get (action) {
        var data = ''
        log.info('request native interface: ' + actions[action])
        try {
            data = new Function('return window.JsInteraction.' + actions[action] + '()')() // eslint-disable-line
        } catch (e) {
            log.info('JsInteraction error: ' + e)
        }
        return data
    },
    setTitle (data) {
        document.title = data
        try {
            window.JsInteraction.setTitle(data)
        } catch (e) {
            log.info('JsInteraction error: ' + e)
        }
    },

    toast (content, time) {
        log.info('request native interface: toast')
        try {
            window.JsInteraction.toast(content, time)
        } catch (e) {
            log.info('JsInteraction error: ' + e)
        }
    },

    setBIEvent (KEY, map, level) {
        try {
            window.JsInteraction.setBIEvent(KEY, map, level)
        } catch (e) {
            log.info('JsInteraction error: ' + e)
        }
    },

    closeWeb () {
        try {
            window.JsInteraction.closeWeb()
        } catch (e) {
            log.info('JsInteraction error: ' + e)
        }
    },

    sendServerErrorMsg () {
        log.info('request native interface: sendServerErrorMsg')
        try {
            window.JsInteraction.sendServerErrorMsg()
        } catch (e) {
            log.info('JsInteraction error: ' + e)
        }
    },

    sendNoNetMsg () {
        log.info('request native interface: sendNoNetMsg')
        try {
            window.JsInteraction.sendNoNetMsg()
        } catch (e) {
            log.info('JsInteraction error: ' + e)
        }
    },
    sendServerErrMsg () {
        log.info('request serverErrorMsg otherCloudReq#')
        if (localStorage.getItem('retryTimes') == null) {
            log.info('first retryTimes')
            localStorage.setItem('retryTimes', 0)
        }
        let retryTimesValue = localStorage.getItem('retryTimes')
        if (retryTimesValue >= config.RETRY_TIMES) {
            // 表盘市场异常，请联系华为客服
            log.info('request native interface: sendWatchFaceServerErrorMsg')
            try {
                window.JsInteraction.sendWatchFaceServerErrorMsg()
            } catch (e) {
                log.info('JsInteraction error: ' + e)
            }
            localStorage.removeItem('retryTimes')
        } else {
            localStorage.setItem('retryTimes', ++retryTimesValue)
            // 服务器异常，请点击重试
            log.info('request native interface: sendWatchFaceServerRetryMsg')
            try {
                window.JsInteraction.sendWatchFaceServerRetryMsg()
            } catch (e) {
                log.info('JsInteraction error: ' + e)
            }
        }
        log.info('retryTimes otherCloudReq# ' + retryTimesValue)
    },

    sendStartDownload (hitopid, version, fileUrl, fileSize) {
        log.info('request native interface: sendStartDownload')
        try {
            window.JsInteraction.sendStartDownload(hitopid, version, fileUrl, fileSize)
        } catch (e) {
            log.info('JsInteraction error: ' + e)
        }
    },

    setDefaultWatchFace (hitopid, version) {
        log.info('request native interface: setDefaultWatchFace')
        try {
            window.JsInteraction.setDefaultWatchFace(hitopid, version)
        } catch (e) {
            log.info('JsInteraction error: ' + e)
        }
    },

    cancelInstallWatchFace (hitopid, version) {
        log.info('request native interface: cancelInstallWatchFace')
        try {
            window.JsInteraction.cancelInstallWatchFace(hitopid, version)
        } catch (e) {
            log.info('JsInteraction error: ' + e)
        }
    },

    deleteWatchFace (hitopid, version) {
        log.info('request native interface: deleteWatchFace')
        try {
            window.JsInteraction.deleteWatchFace(hitopid, version)
        } catch (e) {
            log.info('JsInteraction error: ' + e)
        }
    },

    setWatchFaceDeleteButton (hitopid, version) {
        log.info('request native interface: setWatchFaceDeleteButton')
        try {
            window.JsInteraction.setWatchFaceDeleteButton(hitopid, version)
        } catch (e) {
            log.info('JsInteraction error: ' + e)
        }
    },

    getWatchFaceNames (hitopids) {
        log.info('request native interface: getWatchFaceNames')
        try {
            window.JsInteraction.getWatchFaceNames(hitopids)
        } catch (e) {
            log.info('JsInteraction error: ' + e)
        }
    }
}



