<template>
<div class='detail'>
    <v-tipsBar/>
    <div v-show='isSHowPage'  :class="{reversal: isMiddleEastLanguage}">
        <div class='preview' :style="{marginTop: !isShowTipsBar ? '0rem' : tipsHeight}">
            <div class='allImg'  ref='allimg' :class="allImgMarginClass" >
               <div v-if="isCassini"  @click="showFullPage(index)" class="category">
                    <img v-show="hasImg" :src='imgPath'>
                    <div v-show="!hasImg"></div>
                </div>
                <div v-else  v-for="(item, index) in imgList" :key='item.index' @click="showFullPage(index)" class="category" :style="{float:isMiddleEastLanguage?'right':'left'}">
                  <img v-show="hasImg" :src='fileHost+id+"/"+imgList[index]'>
                  <div v-show="!hasImg"></div>
                </div>
            </div>
        </div>
        <div class='sizeAndCount' v-show="showSizeCount" :class="[isMiddleEastLanguage? 'sizeAndCountReversal': 'sizeAndCountNormal']">
            <p class='size font_13'>{{watchThemeSize}}</p>
            <p class='vertical-line'></p>
            <p class='count font_13'><span>{{downloadTimes}}</span></p>
        </div>
        <div class='font_15 title' :class="{reversal:isMiddleEastLanguage}">{{$t('IDS_watchface_introduction_of_watch_face')}}</div>
        <div class='font_13 content' :class="[{reversal:isMiddleEastLanguage},{contentTopMine:!showSizeCount}]" v-html="summary" :style="{marginTop: !isShowTipsBar ? '0rem' : tipsHeight}"></div>
        <v-down :dialList ='dataAllList' :fileHost= 'fileHost' :isDetail='true'></v-down>
        <transition fadein>
            <div class='bigPic' v-show="isShowBig" @click="showFullPage">
              <div class="imgSize">
                <img :src='showPicSrc' v-show="hasImg">
              </div>
            </div>
        </transition>
    </div>
  </div>
</template>

<script>
import log from '../assets/js/log.js'
import jsInjection from '../assets/js/jsInjectionj.js'
import config from '../assets/js/config.js'
import down from '@/components/common/downBtn'
import tipsBar from '@/components/common/tipsBar'
import {mapGetters} from 'vuex'

export default{
    name: 'watchThemeDetail',
    computed: {
        ...mapGetters([
            'isMiddleEastLanguage',
            'isBigCD',
            'isShowTipsBar',
            'tipsHeight'
        ])
    },
    data () {
        return {
            dataAllList: {},
            fileHost: '',
            imgList: [],
            id: '',
            isShowBig: false,
            showPicSrc: '',
            summary: '',
            watchThemeSize: 0,
            watchThemeWidth: 25.6,
            isSHowPage: false,
            downloadTimes: this.$t('IDS_watchface_download_times.one'),
            hasImg: true,
            showSizeCount: true,
            imgPath: '',
            isCassini: false, // ÊÇ·ñÊÇCassiniÊÖ±í
            allImgMarginClass: []
        }
    },
    methods: {
        setwidth () {
            var dom = this.$refs.allimg
            dom.style.width = (dom.children.length * this.watchThemeWidth + 6.0) + 'rem'
            log.info('dom.style.width: ' + dom.style.width)
            this.isSHowPage = true
        },
        showFullPage (src) {
            if (!this.hasImg) {
                return
            }
            if (this.isShowBig) {
                this.isShowBig = false
            } else {
                this.isShowBig = true
                if (this.dataAllList.pushNum === config.WATCH_CASSINI) {
                    log.info(config.WATCH_FACE_PATHPREFIX + this.dataAllList.previews)
                    this.showPicSrc = config.WATCH_FACE_PATHPREFIX + this.dataAllList.previews
                } else {
                    this.showPicSrc = this.fileHost + this.id + '/' + this.imgList[src]
                }
            }
            log.info('src: ' + src)
        },
        toLocaleString (value) {
            var locale = jsInjection.get('locale')
            locale = locale.replace('_', '-')
            var newValue = Number(value).toLocaleString(locale)
            return newValue
        },
        convert (size) {
            this.watchThemeSize = this.middleEastLanguageHandler(this.toLocaleString(Math.round(size / 1024)), 'KB')
            if (Math.round(size / (1024 * 1024) >= 1)) {
                this.watchThemeSize = this.middleEastLanguageHandler(this.toLocaleString(Math.round(size / (1024 * 1024))), 'MB')
            }
        },
        getParams () {
            this.dataAllList = this.$route.query.data
            this.fileHost = this.$route.query.fileHost
            this.showSizeCount = this.$route.query.showSizeCount
            this.id = this.dataAllList.id
            log.info('this.imgList: ' + this.dataAllList.previews)
            this.convert(this.dataAllList.size)
            this.imgList = this.dataAllList.previews.split('|').filter((value, index, array) => {
                if (value === config.ICON_SMALL_JPG || value === config.ICON_SMALL_PNG) {
                    return false
                }
                return true
            })
            this.hasImg = this.dataAllList.previews !== ''
            if (this.dataAllList.pushNum === config.WATCH_CASSINI) {
                this.imgPath = config.WATCH_FACE_PATHPREFIX + this.dataAllList.previews
                this.isCassini = true
            }
            log.info('this.dataAllList: ' + JSON.stringify(this.dataAllList) + ' this.fileHost: ' + this.fileHost)
            this.summary = this.dataAllList.isPresetWatch ? this.$t('IDS_watchface_presetWatch') : this.getBriefInfoLocale()
            this.summary = this.summary.replace(/\n\r/g, '<p>').replace(/\r\n/g, '</p>').replace(/\n/g, '<br/>').replace(/\t/g, '&nbsp;').replace(/\r/g, '')
        },
        getBriefInfoLocale () {
            // 根据系统语言适配本地简介
            let multiBriefInfo = ''
            let locale = jsInjection.get('locale')
            let preLocale = locale.split('_')[0]
            log.info('locale = ' + locale + ',preLocale = ' + preLocale)
            let htmlBriefInfo = this.dataAllList.briefinfo
            if (htmlBriefInfo.indexOf('{') > -1) {
                let briefinfo = JSON.parse(htmlBriefInfo)
                log.info('briefinfo.locale1 = ' + briefinfo[locale])
                if (briefinfo[locale]) {
                    multiBriefInfo = briefinfo[locale]
                    log.info('double# ' + briefinfo[locale])
                }
                if (!multiBriefInfo) {
                    multiBriefInfo = briefinfo[preLocale]
                    log.info('simple# ' + briefinfo[preLocale])
                }
                if (!multiBriefInfo) {
                    multiBriefInfo = briefinfo['en_US'] || briefinfo['en'] || briefinfo['zh_CN']
                    log.info('default# ' + multiBriefInfo)
                }
            } else {
                multiBriefInfo = htmlBriefInfo
            }
            return multiBriefInfo
        },
        multilingualDownloadTimes () {
            log.info('downloadcount**' + this.dataAllList.downloadcount)
            this.jupgeLanguage(this.dataAllList.downloadcount)
        },
        jupgeLanguage (num) {
            if (num === config.DOWNLOAD_TIMES_ZORE) {
                log.info('IDS_watchface_download_times.one')
                this.downloadTimes = this.$t('IDS_watchface_download_times.one')
            } else if (num === config.DOWNLOAD_TIMES_ONE) {
                this.downloadTimes = this.$t('IDS_watchface_download_times.two')
                log.info('IDS_watchface_download_times.two')
            } else if (num === config.DOWNLOAD_TIMES_TWO) {
                this.downloadTimes = this.$t('IDS_watchface_download_times.three')
                log.info('IDS_watchface_download_times.three')
            } else if (num % config.DOWNLOAD_TIMES_HUNDRED >= config.DOWNLOAD_TIMES_THREE && num % config.DOWNLOAD_TIMES_HUNDRED <= config.DOWNLOAD_TIMES_NINE) {
                this.downloadTimes = this.$t('IDS_watchface_download_times.four')
                log.info('IDS_watchface_download_times.four')
            } else if (num % config.DOWNLOAD_TIMES_HUNDRED >= config.DOWNLOAD_TIMES_ELEVEN && num % config.DOWNLOAD_TIMES_HUNDRED <= config.DOWNLOAD_TIMES_NINETY_NINE) {
                this.downloadTimes = this.$t('IDS_watchface_download_times.many')
                log.info('IDS_watchface_download_times.many')
            } else {
                this.downloadTimes = this.$t('IDS_watchface_download_times.other')
                log.info('IDS_watchface_download_times.other')
            }
            log.info('this.downloadTimes.indexOf(%d): ' + this.downloadTimes.indexOf('%d'))
            log.info('this.downloadTimes: ' + this.downloadTimes)
            if (this.downloadTimes.indexOf('%d') > -1) {
                this.downloadTimes = this.downloadTimes.replace('%d', this.toLocaleString(this.dataAllList.downloadcount))
            }
            log.info('this.downloadTimes: ' + this.downloadTimes)
        },
        middleEastLanguageHandler (firstText, secondText) {
            var value
            if (this.isMiddleEastLanguage) {
                value = secondText + firstText
            } else {
                value = firstText + secondText
            }
            return value
        },
        computeAllImgMarginStyle () {
            let allImgMarginClazz = []
            if (this.isMiddleEastLanguage) {
                if (this.isBigCD) {
                    allImgMarginClazz.push('allImgMarginBigCDReversal')
                } else {
                    allImgMarginClazz.push('allImgMarginReversal')
                }
            } else {
                if (this.isBigCD) {
                    allImgMarginClazz.push('allImgMarginBigCDNormal')
                } else {
                    allImgMarginClazz.push('allImgMarginNormal')
                }
            }
            this.allImgMarginClass = allImgMarginClazz
        }
    },
    mounted () {
        this.getParams()
        this.hasImg = this.dataAllList.previews !== ''
        jsInjection.setTitle(this.dataAllList['title-local'])
        setTimeout(() => {
            this.setwidth()
        }, 10)
        this.multilingualDownloadTimes()
        this.computeAllImgMarginStyle()
    },
    components: {
        'v-down': down,
        'v-tipsBar': tipsBar
    }
}
</script>

<style lang='scss' type='stylesheet/scss'>
.detail{
      display:flex;
      flex-direction:column;
    .preview{
        margin: 1.6rem 0 1.6rem 0;
        overflow-x:auto ;
    .allImg{
        margin-top: 1.6rem;
        height: 24rem;
    .category{
        height: 24rem;
        width: 24rem;
        margin-left: 1.6rem;
        float: left;
    img{
        transform: scale(1);
        -webkit-transform: scale(1);
    }
    div{
        transform: scale(1);
        -webkit-transform: scale(1);
        border-radius: 12rem;
        background-color: #f2f2f2;
    }
    }
    }

    }
    .allImgMarginNormal{
      margin-left: 4.4rem;
    }
    .allImgMarginReversal{
      margin-right: 4.4rem;
    }
    .allImgMarginBigCDNormal{
      margin-left: 22.4rem;
    }
    .allImgMarginBigCDReversal{
      margin-right: 22.4rem;
    }
    .preview::-webkit-scrollbar {
        display:none;
    }
    .sizeAndCount{
        height: 1.75rem;
        display: flex;
        flex-direction: row;
        justify-content: center;
        align-items: center;
        .size,.count{
            display: inline-block;
        }
        .vertical-line{
            display: inline-block;
            width: 1px;
            height: 1.75rem;
            background: #E5E5E5;
        }
    }
.title{
  margin-left: 1.6rem;
  margin-right: 1.6rem;
  margin-top: 2.8rem;
  height: 2rem;
  line-height: 2rem;
  color: #1A1A1A;
}
.content{
  margin-top: 1.6rem;
  margin-left: 1.6rem;
  margin-right: 1.6rem;
  color: rgba(0.1,0.1,0.1,0.5);
  position: absolute;
  top:35.35rem;
  bottom: 5rem;
  overflow-y: scroll;
  word-wrap: break-word;
  width: calc(100% - 3.4rem);
  p{
    margin-top: 0.2rem;
  }
}
.content::-webkit-scrollbar {
  display: none;
}
    .sizeAndCountNormal{
        .size{
          margin-right:0.8rem;
        }
        .count{
          margin-left:0.8rem;
        }
    }
    .sizeAndCountReversal{
        .count{
          margin-right:0.8rem;
        }
        .size{
          margin-left:0.8rem;
        }
    }

    .contentTopMine{
         top: 30.55rem;
    }
    .bigPic{
        background-color: #ffffff;
        position: fixed;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        display:flex;
        justify-content:center;
        align-items:center;
        z-index: 100;
        .imgSize{
          width:24rem;
          height: auto;
         img{
            transform: scale(1.3);
            -webkit-transform: scale(1.3);
          }
        }
    }
    .fadein-enter-active, .fadein-leave-active{
        transition: all 0.3s
    }
    .fadein-enter, .fadein-leave-to{
        opacity: 0;
    }
}
</style>



