export default {
    debug (msg) {
        this.print('DEBUG', msg)
    },
    info (msg) {
        this.print('INFO', msg)
    },
    error (msg) {
        print('ERROR', msg)
    },
    warn (msg) {
        this.print('WARN', msg)
    },

    print (type, msg) {
        msg = ' [Health Dail] ' + msg
        try {
            window.JsInteraction.log(type, msg)
        } catch (e) {
            console.log(type + ' : ' + msg)
        }
    }

}



