<template>
    <ul class="dialUl">
        <li  v-for='(item, index) in dataAllList' class='recommendList' :class="recomDialItemClass" :key='item.index'>
            <router-link :to="{path:'/watchThemeDetail',query:{data:dataAllList[index],fileHost:fileHost,showSizeCount:true}}">
                <v-singleDial class='recomDialListClass' :dialList ='dataAllList[index]' :fileHost= 'fileHost'></v-singleDial>
            </router-link>
            <div v-if='dataAllList[index].watchThemeModle === watchThemeAdded || dataAllList[index].watchThemeModle === watchThemePreset'
                 class="download font_13" :class="{reversal:isMiddleEastLanguage}">{{installedText}}</div>
            <div v-else-if='dataAllList[index].watchThemeModle === watchThemeDefault' class="download font_13" :class="{reversal:isMiddleEastLanguage}">{{installedText}}</div>
            <v-down v-else :dialList ='dataAllList[index]' :fileHost= 'fileHost' :btnFontSize='btnFontSize'></v-down>
        </li>
    </ul>
</template>

<script>
import singleDial from '@/components/common/singleDial'
import config from '../assets/js/config.js'
import down from '@/components/common/downBtn'
import jsInjectionj from '../assets/js/jsInjectionj'
import log from '../assets/js/log'
import {mapGetters} from 'vuex'
export default {
    name: 'index',
    props: {
        'dataAllList': {
            type: Array,
            default: () => {
                return []
            }
        },
        'fileHost': {
            type: String,
            default: ''
        }
    },
    computed: {
        ...mapGetters([
            'isMiddleEastLanguage',
            'isBigCD'
        ])
    },
    data () {
        return {
            installedText: this.$t('IDS_watchface_installed'),
            defaultText: this.$t('IDS_watchface_default'),
            watchThemeAdded: config.WATCH_THEME_TYPE_ADD,
            watchThemePreset: config.WATCH_THEME_TYPE_REESET,
            watchThemeDefault: config.WATCH_THEME_TYPE_DEFAULT,
            btnFontSize: 'font_13',
            recomDialItemClass: []
        }
    },
    components: {
        'v-singleDial': singleDial,
        'v-down': down
    },
    created () {
        this.btnFontSize = jsInjectionj.get('locale')
        log.info('marketBtn# ' + this.btnFontSize)
    },
    mounted () {
        // 适配大溪地样式
        this.computeRecomDialItemStyle()
    },
    methods: {
        computeRecomDialItemStyle () {
            let recomDialItemClazz = []
            if (this.isBigCD) {
                if (this.isMiddleEastLanguage) {
                    recomDialItemClazz.push('recomDialListClassBigCDReversal')
                } else {
                    recomDialItemClazz.push('recomDialListClassBigCDNormal')
                }
            } else {
                if (this.isMiddleEastLanguage) {
                    recomDialItemClazz.push('recomDialListClassReversal')
                } else {
                    recomDialItemClazz.push('recomDialListClassNormal')
                }
            }
            this.recomDialItemClass = recomDialItemClazz
        }
    }
}
</script>

<style lang="scss" type="stylesheet/scss">
.dialUl{
    overflow: hidden;
    .recommendList{
        position: relative;
        width: 15.55rem;
        height: 20.65rem;
        padding:0.8rem;
        box-sizing: border-box;
        .download{
            height: 2.8rem;
            width: 7.2rem;
            line-height: 2.8rem;
            color: #1A1A1A;
            position: absolute;
            bottom:0.8rem;
            left: 4.175rem;
            text-align: center;
            overflow: hidden;
            text-overflow:ellipsis;
            white-space: nowrap;
        }
    }
    .recommendList:active{
      background:#f2f2f2;
      border-radius: 0.4rem;
    }
    .recomDialListClassNormal{
        float: left;
    }
    .recomDialListClassReversal{
        float: right;
    }
    .recomDialListClassNormal:nth-child(odd){
      margin: 0.8rem 0.55rem 0.6rem 1.9rem
    }
    .recomDialListClassNormal:nth-child(even){
      margin: 0.8rem 1.9rem 0.6rem 0.55rem
    }
    .recomDialListClassReversal:nth-child(odd){
      margin: 0.8rem 1.9rem 0.6rem 0.55rem
    }
    .recomDialListClassReversal:nth-child(even){
      margin: 0.8rem 0.55rem 0.6rem 1.9rem
    }
    .recomDialListClassBigCDNormal{
      float: left;
    }
    .recomDialListClassBigCDReversal{
      float: right;
    }
    .recomDialListClassBigCDNormal:nth-child(4n+1){
      margin: 0.8rem 1rem 0.6rem 1.9rem
    }
    .recomDialListClassBigCDNormal:nth-child(4n+2){
      margin: 0.8rem 1rem 0.6rem 1rem
    }
    .recomDialListClassBigCDNormal:nth-child(4n+3){
      margin: 0.8rem 1rem 0.6rem 1rem
    }
    .recomDialListClassBigCDNormal:nth-child(4n+4){
      margin: 0.8rem 1.9rem 0.6rem 1rem
    }
    .recomDialListClassBigCDReversal:nth-child(4n+1){
      margin: 0.8rem 1.9rem 0.6rem 1rem
    }
    .recomDialListClassBigCDReversal:nth-child(4n+2){
      margin: 0.8rem 1rem 0.6rem 1rem
    }
    .recomDialListClassBigCDReversal:nth-child(4n+3){
      margin: 0.8rem 1rem 0.6rem 1rem
    }
    .recomDialListClassBigCDReversal:nth-child(4n+4){
      margin: 0.8rem 1rem 0.6rem 1.9rem
    }
@media (max-width: 320px) {
  .recommendList{
    position: relative;
    width: 12.21rem;
    height: 18.28rem;
  .download{
    height: 2.376rem;
    width: 6.27rem;
    line-height: 2.376rem;
    color: #1A1A1A;
    position: absolute;
    bottom: 0;
    left: 2.97rem;
    text-align: center;
    overflow: hidden;
    text-overflow:ellipsis;
    white-space: nowrap;
  }
}
.recomDialListClassNormal:nth-child(odd){
  margin: 1.6rem 1.188rem 1.4rem 2.376rem
}
.recomDialListClassNormal:nth-child(even){
  margin: 1.6rem 2.376rem 1.4rem 1.188rem
}
.recomDialListClassReversal:nth-child(odd){
  margin: 1.6rem 2.376rem 1.4rem 1.188rem
}
.recomDialListClassReversal:nth-child(even){
  margin: 1.6rem 1.188rem 1.4rem 2.376rem
}
}
}
</style>



