import Vue from 'vue'
import Router from 'vue-router'
import Index from '@/components/Index'
import recommend from '@/components/recommend'
import myDial from '@/components/myDial'
import detail from '@/components/watchThemeDetail'

Vue.use(Router)

export default new Router({
    routes: [
        {
            path: '/',
            name: 'Index',
            component: Index,
            meta: {
                keepAlive: true
            }
        },
        {
            path: '/index',
            name: 'Index',
            component: Index,
            meta: {
                keepAlive: true
            }
        },
        {
            path: '/recommend',
            name: 'recommend',
            component: recommend,
            meta: {
                keepAlive: true
            }
        },
        {
            path: '/myDial',
            name: 'myDial',
            component: myDial,
            meta: {
                keepAlive: true
            }
        },
        {
            path: '/watchThemeDetail',
            name: 'detail',
            component: detail
        }
    ]
})



