<template>
    <div class="downloadFrame">
        <div v-if="dialList.watchThemeModle === 2" class="bottomBtnStyle font_13" :class="[downloadButtonClass,isDetail? (isBigCD?'buttonLeftBigCD':'buttonLeft'):'']"
            :style="{width: isBigCD && isDetail ? '40rem' :buttonWidth}">
            <img class="installCancel" :class="installCancelIconClass" src="../../assets/img/ic_close.png" v-show="showInstallCancel" @click="cancelInstall"/>
            <div @click="downFile">
                <div class='progress' :class="[{reversal:isMiddleEastLanguage},isMiddleEastLanguage ? 'progressRight':'progressLeft',btnFontSize]" :style="{width: progressWidth}"></div>
                <p class='textContent' :class="btnFontSize">{{text}}</p>
            </div>
        </div>
      <div v-else @click="setDefaultWatchFace" class="downloaded font_13" :class="[isDetail ?(isBigCD?'buttonLeftBigCD':'buttonLeft'):'',isDetail ? 'detailButton':'marketButton', isMiddleEastLanguage ? 'reversal' : '',isDefaultDial ? ['defaultLightColor','gray-light-border']:'gray-border']" :style="{width: isBigCD && isDetail ? '40rem' :buttonWidth }" >
        {{text}}
      </div>
    </div>
</template>

<script>
import log from '../../assets/js/log.js'
import jsInjection from '../../assets/js/jsInjectionj.js'
import CloudRequest from '../../assets/js/otherCloudReq.js'
import config from '../../assets/js/config.js'
import setBiEvent from '../../assets/js/setBiEvent'
import {mapGetters} from 'vuex'

export default {
    props: {
        'dialList': {
            type: Object,
            default: null
        },
        'fileHost': {
            type: String,
            default: ''
        },
        'isDetail': {
            type: Boolean,
            default: false
        },
        'btnFontSize': {
            type: String,
            default: ''
        }
    },
    data () {
        return {
            showInstallCancel: false,
            isDownloading: false,
            text: '',
            progress: 0,
            isDefaultDial: false,
            timer: null,
            statusDefault: this.$t('IDS_watchface_default'),
            statusInstalled: this.$t('IDS_watchface_installed'),
            statusSetDefault: this.$t('IDS_watchface_set_default'),
            statusUnInstalled: this.$t('IDS_watchface_install'),
            presetWatch: this.$t('IDS_watchface_presetWatch'),
            statusUpdate: this.$t('IDS_watchface_update'),
            downloadingText: this.$t('IDS_watchface_downloading'),
            installingText: this.$t('IDS_watchface_installing'),
            downloadingAndProgressText: this.$t('IDS_watchface_downloading_and_progress'),
            installingAndProgressText: this.$t('IDS_watchface_installing_and_progress'),
            buttonWidth: this.isDetail ? '20rem' : '7.2rem',
            progressWidth: '0rem',
            downloadButtonClass: [],
            waitingNativeNotifyBack: false,
            currentWatchFaceIdStore: '',
            clickTimeout: true,
            installCancelIconClass: [],
            dialDownloading: false,
            startDownloadTime: 0,
            endInstalledTime: 0
        }
    },
    methods: {
        clearDownloadingWatchFaceId () {
            log.info('clearDownloadingWatchFaceId' + this.dialList.hitopid)
            localStorage.removeItem(config.CURRENT_DOWNLOADING_WATCH_ID)
        },
        clearDownloadingProgress () {
            log.info('clearDownloadingProgress#' + this.dialList.hitopid)
            localStorage.removeItem(this.dialList.hitopid)
        },
        clearDownloadingStatus () {
            log.info('clearDownloadingStatus#')
            localStorage.removeItem(config.DIAL_DWONLOADING_STATUS)
        },
        downFile () {
            log.info('isDeviceConnected#' + this.isDeviceConnected)
            if (!this.clickTimeout) {
                return
            }
            this.clickTimeout = false
            setTimeout(() => {
                this.clickTimeout = true
            }, config.CLICK_TIMEOUT)
            let currentDownloadId = localStorage.getItem(config.CURRENT_DOWNLOADING_WATCH_ID)
            let downloadingId = 0
            if (currentDownloadId && currentDownloadId.length > 0) {
                downloadingId = parseInt(currentDownloadId.split('_')[0])
            }
            let state = parseInt(jsInjection.get('getWatchFaceInstallState'))
            log.info('downFile#' + this.isDownloading + ', ' + currentDownloadId + ', ' + state)
            if (state === config.NATIVE_INSTALL_STATE_IDLE) {
                this.clearDownloadingWatchFaceId()
                localStorage.removeItem(currentDownloadId)
                currentDownloadId = ''
            }
            // 正在下载->返回
            if (this.isDownloading || downloadingId > 0 || (!isNaN(state) && state !== config.NATIVE_INSTALL_STATE_IDLE)) {
                // 弹只能下载一次的Toast
                if (!this.isDownloading) {
                    let downloadLimit = this.$t('IDS_watchface_downloadLimit')
                    log.info('toast: ' + downloadLimit)
                    jsInjection.toast(downloadLimit, 1)
                }
                return
            }
            this.addInstallEventListener() // 添加安装监听事件
            this.progress = 0
            // 进度条宽度
            this.progressWidth = this.progress + '%'
            // 通知native开始下载
            var fileUlr = this.fileHost + this.dialList.id + '/' + this.dialList.packagefile
            log.info('downFile#' + this.dialList.id + ', ' + this.dialList.hitopid)
            var size = Math.round(this.dialList.size / config.FILESIZE_KB)
            var fileSize = ''
            if (size > config.FILESIZE_KB) {
                size = Math.round(this.dialList.size / config.FILESIZE_MB)
                fileSize = size + 'MB'
            } else {
                fileSize = size + 'KB'
            }
            this.startDownloadTime = Date.now()
            localStorage.setItem(config.START_DOWNLOAD_TIME, this.startDownloadTime)
            jsInjection.sendStartDownload(this.dialList.hitopid, this.dialList.version, fileUlr, fileSize)
        },
        handleProgress (currentHitopid, progress) {
            log.info('currentHitopid = ' + currentHitopid + ',progress = ' + progress)
            var hitopid = currentHitopid
            var currentProgress = progress
            // 保存下载进度
            localStorage.setItem(hitopid, currentProgress)
            // 保存当前下载的表盘id
            localStorage.setItem(config.CURRENT_DOWNLOADING_WATCH_ID, hitopid + '_' + this.dialList.version)
            if (this.dialList.hitopid === hitopid) {
                this.progress = currentProgress
                // 下载中的进度，为100时，进度条不填充颜色
                if (this.dialDownloading && currentProgress === 100) {
                    this.progress = 0
                }
                // 进度条宽度
                this.progressWidth = this.progress + '%'
                if (this.dialDownloading) {
                    this.detailTextHandler(this.downloadingAndProgressText, this.downloadingText)
                } else {
                    this.detailTextHandler(this.installingAndProgressText, this.installingText)
                }
                // 保存传输进度
                this.dialList.progress = this.progress
                // 显示安装|取消
                if (this.isDetail) {
                    this.showInstallCancel = this.isDetail
                }
                this.isDownloading = true
                // 接收到native进度，计算样式
                this.computeDownButtonStyle()
                // 检测安装是否完成
                if (!this.dialDownloading) {
                    this.checkInstallStatus(currentProgress)
                }
            }
        },
        // 下载进度监听
        downloadWatchFaceProgress (event) {
            var data = event.data
            log.info('downloadWatchFaceProgress# ' + data.hitopid + ' ' + data.downloadProgress)
            this.dialDownloading = true
            localStorage.setItem(config.DIAL_DWONLOADING_STATUS, this.dialDownloading)
            // 下载进度过滤只显示能被10整除的数，解决下载速度太快页面刷新太快卡顿问题
            if (data.downloadProgress % config.DOWNLOAD_PROGRESS_FILTER === 0 || data.downloadProgress === config.DOWNLOAD_HUNDRED - 1) {
                this.handleProgress(data.hitopid, data.downloadProgress)
            }
        },
        // 下载结果
        downloadWatchFaceResult (event) {
            var data = event.data
            log.info('downloadWatchFaceResult# ' + data.hitopid + ', ' + data.version + ', ' + data.resultCode + 'resultCode type' + typeof data.resultCode)
            log.info('this.dialList.hitopid = ' + this.dialList.hitopid)
            if (this.dialList.hitopid === data.hitopid) {
                log.info('download result#')
                this.clearDownloadingStatus()
                if (data.resultCode === config.DOWNLOAD_WATCH_FACE_SUCCESS) {
                    //  下载成功后开始传输及取消下载失败
                    log.info('downloadWatchFaceResult addInstallEventListener#')
                    this.dialDownloading = false
                    this.computeButtonText()
                } else if (data.resultCode === config.DOWNLOAD_WATCH_FACE_FAIL || data.resultCode === config.CANCEL_DOWNLOAD_WATCH_FACE_SUCCESS) {
                    // 下载表盘失败及取消下载成功
                    log.info('downloadWatchFaceResult: Fail')
                    // 下载失败bi打点
                    setBiEvent.installAndUpdateFail(this.dialList.hitopid, this.dialList.version, '3')
                    this.resetUnInstalled()
                }
            }
        },
        transmitProgress (event) {
            var data = event.data
            log.info('transmitProgress: ' + data.hitopid + ', ' + data.currentProgress)
            this.handleProgress(data.hitopid, data.currentProgress)
        },
        checkInstallStatus (newProgress) {
            if (newProgress >= config.INSTALL_PROGRESS_COMPLETE) {
                log.info('checkInstallStatus: installComplete')
                window.isDownloadingWatchFace = false
            }
        },
        downloadFeedback () {
            log.info('downloadFeedback#' + this.dialList.hitopid)
            var params = {
                sign: this.apiSign,
                type: config.FEEDBACK_TYPE_WATCH,
                id: this.dialList.id
            }
            CloudRequest.request('downloadFeedback', params, data => {
                log.debug('downloadFeedback#result = ' + JSON.stringify(data))
            }, function (info) {
                log.debug('downloadFeedback#info = ' + JSON.stringify(info))
            })
        },
        toLocaleString (value) {
            var locale = jsInjection.get('locale')
            locale = locale.replace('_', '-')
            var newValue = Number(value).toLocaleString(locale)
            return newValue
        },
        computeButtonText () {
            // 默认表盘
            if (this.isDefaultDial) {
                // 默认表盘id和版本号都相同
                let preWatchFaceIdStore = jsInjection.get('preWatchFaceIdStore').split(',')
                let presetWatchIds = preWatchFaceIdStore.map((idVersion) => {
                    return idVersion.split('_')[0]
                })
                this.dialList.presetWatch = presetWatchIds.indexOf(this.dialList.hitopid) > -1
                if (this.dialList.presetWatch) {
                    this.dialList.watchThemeModle = config.WATCH_THEME_TYPE_DEFAULT
                }
                if (this.dialList.hitopid + '_' + this.dialList.version === this.currentWatchFaceIdStore || this.dialList.presetWatch) {
                    this.text = this.isDetail ? this.statusDefault : this.statusInstalled
                } else {
                    this.text = this.getWatchFileSize()
                }
                this.computeDownButtonStyle()
            } else {
                // 详情页面显示 设为默认
                // 首页页面显示 已安装
                if (this.dialList.watchThemeModle === config.WATCH_THEME_TYPE_ADD) { // 已安装
                    this.text = this.isDetail ? this.statusSetDefault : this.statusInstalled
                } else if (this.dialList.watchThemeModle === config.WATCH_THEME_TYPE_REESET) { // 预置表盘
                    this.text = this.isDetail ? this.statusSetDefault : this.presetWatch
                } else { // 未安装或者正在下载中
                    // 二级页面上有从一级页面上传递下来的进度
                    if (this.progress > 1 || this.isDownloading) {
                        if (this.dialDownloading) {
                            log.info('download#')
                            this.detailTextHandler(this.downloadingAndProgressText, this.downloadingText)
                        } else {
                            log.info('transmit#')
                            this.detailTextHandler(this.installingAndProgressText, this.installingText)
                        }
                    } else {
                        this.text = this.getWatchFileSize()
                    }
                    this.computeDownButtonStyle()
                }
            }
        },
        detailTextHandler (detailText, listText) {
            if (this.isDetail) {
                this.text = detailText.replace('%s', this.languageOverturn())
            } else {
                this.text = listText
            }
        },
        languageOverturn () {
            var progressText
            if (this.isMiddleEastLanguage) {
                progressText = '%' + this.toLocaleString(this.progress)
            } else {
                progressText = this.toLocaleString(this.progress) + '%'
            }
            return progressText
        },
        computeDownButtonStyle () {
            var downClazz = []
            if (this.isDetail) { // 表盘详情页
                downClazz.push(this.isDownloading ? 'detailButton-downloading' : 'detailButton-download')
            } else { // 表盘市场页
                downClazz.push('marketButton')
                if (!this.isDownloading) {
                    downClazz.push('gray-border')
                }
            }
            if (this.isDownloading) {
                downClazz.push('downloading-border')
            }
            this.downloadButtonClass = downClazz
        },
        getWatchFileSize () {
            var isInstalled = this.installedWatchIds.indexOf(this.dialList.hitopid) > -1
            var preText = isInstalled ? this.statusUpdate : this.statusUnInstalled
            if (!this.isDetail) {
                return preText
            }
            var size = Math.round(this.dialList.size / config.FILESIZE_KB)
            if (size > config.FILESIZE_KB) {
                size = Math.round(this.dialList.size / config.FILESIZE_MB)
                return this.languageOverturnText(preText, size, 'MB')
            } else {
                return this.languageOverturnText(preText, size, 'KB')
            }
        },
        languageOverturnText (preText, size, sizeUnit) {
            if (this.isMiddleEastLanguage) {
                return preText + '(' + sizeUnit + this.toLocaleString(size) + ')'
            } else {
                return preText + ' (' + this.toLocaleString(size) + sizeUnit + ')'
            }
        },
        // 从native获取该表盘的版本号
        getInstalledWatchFaceVersion () {
            let version = ''
            let watchFaceIdStore = jsInjection.get('watchFaceIdStore').split(',')
            watchFaceIdStore.filter((value, index, arr) => {
                let idAndVersion = value.split('_')
                if (idAndVersion[0] === this.dialList.hitopid) {
                    log.info('setDefaultWatchFace#UnfoundedWatch: ' + idAndVersion[1])
                    version = idAndVersion[1]
                }
                return false
            })
            return version
        },
        // 设为默认
        setDefaultWatchFace () {
            if (this.isDetail && this.dialList.watchThemeModle === config.WATCH_THEME_TYPE_DEFAULT) {
                return
            }
            var version = this.getInstalledWatchFaceVersion()
            // 预置表盘
            if (this.dialList.isPresetWatch) {
                let preWatchFaceIdStore = jsInjection.get('preWatchFaceIdStore').split(',')
                preWatchFaceIdStore.filter((value, index, arr) => {
                    let idAndVersion = value.split('_')
                    if (idAndVersion[0] === this.dialList.hitopid) {
                        log.info('setDefaultWatchFace#PresetWatch: ' + idAndVersion[1])
                        version = idAndVersion[1]
                    }
                    return false
                })
            }
            // 表盘版本为空, 如果在云端未查询到该表盘，还需要从native获取该表盘的版本号
            if (!version) {
                version = this.getInstalledWatchFaceVersion()
            }
            log.info('setDefaultWatchFace: ' + this.dialList.hitopid + ', ' + version)
            jsInjection.setDefaultWatchFace(this.dialList.hitopid, version)
            this.addInstallEventListener()
        },
        transmitSetDefaultWatchFaceResult (event) {
            var data = event.data
            log.info('transmitSetDefaultWatchFaceResult#' + data.hitopid + ', result = ' + data.result)
            if (this.dialList.hitopid === data.hitopid) {
                // 设为默认成功
                if (data.result === config.NATIVE_OPERATION_SUCCESS) {
                    log.info('transmitSetDefaultWatchFaceResult# 1')
                    // 设为当前表盘成功bi打点
                    setBiEvent.setCurrentWatchFace(this.dialList.hitopid, this.dialList.version, 1)
                    log.info('setDefault: Success')
                    this.dialList.watchThemeModle = config.WATCH_THEME_TYPE_DEFAULT
                    this.$store.dispatch('setWatchInfoChanged', true)
                    this.$store.dispatch('setDefaultWatchId', this.dialList.hitopid)
                    this.isDefaultDial = true
                    this.currentWatchFaceIdStore = this.dialList.hitopid + '_' + this.dialList.version
                    this.computeButtonText()
                    this.removeInstallListener()
                } else {
                    log.info('transmitSetDefaultWatchFaceResult# 0')
                    // // 设为当前表盘失败bi打点
                    setBiEvent.setCurrentWatchFace(this.dialList.hitopid, this.dialList.version, 0)
                    log.info('setDefault: Fail')
                    // 设为默认失败
                }
            }
        },
        // 取消安装
        cancelInstall () {
            log.info('cancelInstall: ' + this.dialList.hitopid + ', ' + this.dialList.version)
            jsInjection.cancelInstallWatchFace(this.dialList.hitopid, this.dialList.version)
        },
        // 安装失败
        transmitInstallWatchFaceResult (event) {
            log.info('transmitInstallWatchFaceResult#' + event.data.hitopid + ', result = ' + event.data.result)
            this.removeInstallListener()
            this.progress = 0
            // 进度条宽度
            this.progressWidth = this.progress + '%'
            this.dialList.progress = this.progress
            if (this.dialList.hitopid === event.data.hitopid && event.data.result === config.NATIVE_OPERATION_FAIL) {
                // bi打点：1 内存不足 2 蓝牙断连 3 下载失败
                if (this.isDeviceConnected) {
                    setBiEvent.installAndUpdateFail(this.dialList.hitopid, this.dialList.version, '1')
                } else {
                    setBiEvent.installAndUpdateFail(this.dialList.hitopid, this.dialList.version, '2')
                }
                log.info('transmitInstallWatchFaceResultFail')
                // 隐藏按钮
                this.showInstallCancel = false
                this.isDownloading = false
                this.computeButtonText()
                // 设为当前表盘失败bi打点
                setBiEvent.setCurrentWatchFace(this.dialList.hitopid, this.dialList.version, 0)
            } else if (this.dialList.hitopid === event.data.hitopid && event.data.result === config.NATIVE_OPERATION_SUCCESS) {
                this.$store.dispatch('setWatchInfoChanged', true)
                this.showInstallCancel = false
                this.isDownloading = false
                this.dialList.watchThemeModle = config.WATCH_THEME_TYPE_ADD
                this.isDefaultDial = false
                this.computeButtonText()
                this.endInstalledTime = Date.now()
                // 安装表盘成功时长bi打点
                setBiEvent.installAndUpdateSuccess(this.dialList.hitopid, this.dialList.version, this.endInstalledTime - this.startDownloadTime)
                localStorage.removeItem(config.START_DOWNLOAD_TIME)
                // 下载反馈
                this.downloadFeedback()
                // 设为默认
                log.info('setDefaultWatchFace: ' + this.dialList.hitopid + ', ' + this.dialList.version)
                jsInjection.setDefaultWatchFace(this.dialList.hitopid, this.dialList.version)
                // 设为当前表盘成功bi打点
                setBiEvent.setCurrentWatchFace(this.dialList.hitopid, this.dialList.version, 1)
            }
            this.clearDownloadingProgress()
            this.clearDownloadingWatchFaceId()
            window.defaultWatchChanged()
        },
        resetUnInstalled () {
            this.clearDownloadingProgress()
            // 隐藏按钮
            this.showInstallCancel = false
            this.isDownloading = false
            this.progress = 0
            this.dialList.progress = this.progress
            // 进度条宽度
            this.progressWidth = this.progress + '%'
            this.computeButtonText()
            this.clearDownloadingWatchFaceId()
            this.removeInstallListener()
        },
        transmitCancelInstallResult (event) {
            var data = event.data
            log.info('transmitCancelInstallResult#' + data.hitopid)
            if (this.dialList.hitopid === data.hitopid) {
                // 取消安装成功
                if (data.result === config.NATIVE_OPERATION_SUCCESS) {
                    this.resetUnInstalled()
                }
            }
        },
        addInstallEventListener () {
            this.removeInstallListener()
            // 取消安装
            window.addEventListener('transmitCancelInstallWatchFaceResult', this.transmitCancelInstallResult, true)
            // 设为默认
            window.addEventListener('transmitSetDefaultWatchFaceResult', this.transmitSetDefaultWatchFaceResult, true)
            // 安装失败异常
            window.addEventListener('transmitInstallWatchFaceResult', this.transmitInstallWatchFaceResult, true)
            // 安装进度监听
            log.info('addInstallEventListener#' + this.dialList.hitopid)
            window.addEventListener('transmitProgress', this.transmitProgress, true)
            // 下载进度
            window.addEventListener('downloadWatchFaceProgress', this.downloadWatchFaceProgress, true)
            // 下载结果
            window.addEventListener('downloadWatchFaceResult', this.downloadWatchFaceResult, true)
        },
        removeInstallListener () {
            window.removeEventListener('transmitProgress', this.transmitProgress, true)
            window.removeEventListener('transmitCancelInstallWatchFaceResult', this.transmitCancelInstallResult, true)
            window.removeEventListener('transmitSetDefaultWatchFaceResult', this.transmitSetDefaultWatchFaceResult, true)
            window.removeEventListener('transmitInstallWatchFaceResult', this.transmitInstallWatchFaceResult, true)
            window.addEventListener('downloadWatchFaceProgress', this.downloadWatchFaceProgress, true)
            window.addEventListener('downloadWatchFaceResult', this.downloadWatchFaceResult, true)
        },
        // 删除表盘成功监听
        transmitDeleteWatchFaceResult (event) {
            var data = event.data
            log.info('transmitDeleteWatchFaceResult#' + data.hitopid + ', result = ' + data.result)
            if (this.dialList.hitopid === data.hitopid && data.result === config.NATIVE_OPERATION_SUCCESS) {
                // 删除成功 -> 返回
                let currentWatchFaceId = jsInjection.get('currentWatchFaceId')
                let currentWatchId = currentWatchFaceId.split('_')[0]
                this.$store.dispatch('setDefaultWatchId', currentWatchId)
                this.$store.dispatch('setWatchInfoChanged', true)
                this.waitingNativeNotifyBack = true
            }
        },
        loadWatchInfoFromNative () {
            let currentWatchFaceId = jsInjection.get('currentWatchFaceId')
            let currentWatchId = currentWatchFaceId.split('_')[0]
            let watchFaceIdStore = jsInjection.get('watchFaceIdStore').split(',')
            let preWatchFaceIdStore = jsInjection.get('preWatchFaceIdStore').split(',')
            let presetWatchIds = preWatchFaceIdStore.map((idVersion) => {
                return idVersion.split('_')[0]
            })
            // 用户点击下载后退出表盘市场页面,直到安装成功后也未再进入页面,需要清空在此之前保存的安装进度
            let installedWatchIds = []
            watchFaceIdStore.forEach((value, index, arr) => {
                let installedId = value.split('_')[0]
                installedWatchIds.push(installedId)
                localStorage.removeItem(value)
                if (value === localStorage.getItem(config.CURRENT_DOWNLOADING_WATCH_ID)) {
                    log.info('clearDownloadingWatchFaceId' + value)
                    localStorage.removeItem(config.CURRENT_DOWNLOADING_WATCH_ID)
                }
            })
            this.$store.commit('setInstalledWatchIds', installedWatchIds)
            let newstr = this.dialList.hitopid + '_' + this.dialList.version
            this.dialList.watchThemeModle = config.WATCH_THEME_TYPE_DOWNLOAD_NO
            this.dialList.isPresetWatch = false
            this.$store.dispatch('setDefaultWatchId', currentWatchId)
            if (currentWatchId === this.dialList.hitopid) {
                this.dialList.watchThemeModle = config.WATCH_THEME_TYPE_DEFAULT
                this.dialList.isPresetWatch = presetWatchIds.indexOf(this.dialList.hitopid) > -1
            } else if (watchFaceIdStore.indexOf(newstr) > -1) {
                this.dialList.watchThemeModle = config.WATCH_THEME_TYPE_ADD
            } else if (presetWatchIds.indexOf(this.dialList.hitopid) > -1) {
                this.dialList.watchThemeModle = config.WATCH_THEME_TYPE_REESET
                this.dialList.isPresetWatch = true
            }
        },
        // 检查安装状态,用户手动kill app, 再次进入后, 清空之前缓存的安装进度
        checkNativeDownloadingStatus () {
            let state = parseInt(jsInjection.get('getWatchFaceInstallState'))
            // native没有在下载(进程被杀或手表被重置)
            if (state === config.NATIVE_INSTALL_STATE_IDLE) {
                this.resetUnInstalled()
            }
        },
        reComputeIsDefault () {
            this.currentWatchFaceIdStore = jsInjection.get('currentWatchFaceId')
            let currentWatchId = this.currentWatchFaceIdStore.split('_')[0]
            this.$store.dispatch('setDefaultWatchId', currentWatchId)
            this.isDefaultDial = this.dialList.hitopid === currentWatchId
            if (this.isDetail) {
                if (this.currentWatchFaceIdStore !== this.dialList.hitopid + '_' + this.dialList.version) {
                    this.dialList.watchThemeModle = config.WATCH_THEME_TYPE_DOWNLOAD_NO
                }
            }
        },
        // 从我的页面进入,需要重新计算是否需要显示更新
        reComputeIfUpdateNeeded () {
            let watchFaceIdStore = jsInjection.get('watchFaceIdStore').split(',')
            watchFaceIdStore.filter((data, index, arr) => {
                let idVersion = data.split('_')
                if (idVersion[0] === this.dialList.hitopid && idVersion[1] !== this.dialList.version) {
                    this.dialList.watchThemeModle = config.WATCH_THEME_TYPE_DOWNLOAD_NO
                }
                return false
            })
        },
        updateProgressAndText () {
            // 获取推荐页面下载-安装的进度;
            let progress = localStorage.getItem(this.dialList.hitopid)
            let currentDowningId = localStorage.getItem(config.CURRENT_DOWNLOADING_WATCH_ID)
            // 保存开始时间，解决从列表和详情页切换导致数据丢失问题
            let startDownloadTime = localStorage.getItem(config.START_DOWNLOAD_TIME)
            this.startDownloadTime = startDownloadTime
            if (this.startDownloadTime) {
                let dialStatus = localStorage.getItem(config.DIAL_DWONLOADING_STATUS)
                this.dialDownloading = dialStatus
            }
            log.info('updateProgressAndText: ' + currentDowningId + ',' + progress)
            if (this.dialList.progress > 0 && progress === null && currentDowningId === null) {
                this.dialList.progress = 0
            }
            if (progress && parseInt(progress) >= 0 && currentDowningId === this.dialList.hitopid + '_' + this.dialList.version) {
                this.dialList.progress = progress
                this.checkNativeDownloadingStatus()
            }
            if ((this.dialList.progress && this.dialList.progress < config.INSTALL_PROGRESS_COMPLETE) || this.dialDownloading) {
                log.info('original progress = ' + this.dialList.progress)
                this.progress = this.dialList.progress
                // 进度条宽度
                this.progressWidth = this.progress + '%'
                this.showInstallCancel = this.isDetail
                this.isDownloading = true
                this.addInstallEventListener()
                this.checkNativeDownloadingStatus()
            }
            if (!this.dialList.watchThemeModle) {
                this.loadWatchInfoFromNative()
            }
            this.isDefaultDial = (this.dialList.watchThemeModle === config.WATCH_THEME_TYPE_DEFAULT) || this.dialList.hitopid === this.defaultWatchId
            if (this.isDefaultDial) {
                // 重新从native获取默认表盘数据,判断是更新还是默认
                this.reComputeIsDefault()
            }
            if (this.dialList.watchThemeModle === config.WATCH_THEME_TYPE_ADD) {
                this.reComputeIfUpdateNeeded()
            }
            log.info('mounted: ' + this.isDetail + ', hitopid = ' + this.dialList.hitopid + ', isDefault: ' + this.isDefaultDial + ', modle: ' + this.dialList.watchThemeModle)
            this.computeButtonText()
            // 已安装表盘，设置删除button
            let installedWatches = jsInjection.get('watchFaceIdStore')
            if (installedWatches.indexOf(this.dialList.hitopid) >= 0 && this.isDetail) {
                // 直接从native获取该已安装表盘对应的版本号
                let version = this.getInstalledWatchFaceVersion()
                jsInjection.setWatchFaceDeleteButton(this.dialList.hitopid, version)
                window.addEventListener('transmitDeleteWatchFaceResult', this.transmitDeleteWatchFaceResult, true)
            }
        },
        watchInfoChanged () {
            log.info('watchInfoChanged#' + this.waitingNativeNotifyBack)
            if (this.waitingNativeNotifyBack) {
                this.waitingNativeNotifyBack = false
                this.$router.back(-1)
            } else {
                this.loadWatchInfoFromNative()
                this.updateProgressAndText()
            }
        },
        computeInstallCancelIconStyle () {
            let installCancelIconClazz = []
            if (this.isBigCD) {
                if (this.isMiddleEastLanguage) {
                    installCancelIconClazz.push('installCancelBigCDReversal')
                } else {
                    installCancelIconClazz.push('installCancelBigCDNoraml')
                }
            } else {
                if (this.isMiddleEastLanguage) {
                    installCancelIconClazz.push('installCancelReversal')
                } else {
                    installCancelIconClazz.push('installCancelNoraml')
                }
            }
            this.installCancelIconClass = installCancelIconClazz
        }
    },
    mounted () {
        setTimeout(() => {
            this.updateProgressAndText()
            // 详情页面, 监听 删除
            if (this.isDetail) {
                window.transmitWatchInfoChange = this.watchInfoChanged
            }
        }, 10)
        this.clickTimeout = true
        // 适配大溪地
        this.computeInstallCancelIconStyle()
    },
    activated () {
        setTimeout(() => {
            this.updateProgressAndText()
        }, 10)
        this.clickTimeout = true
    },
    destroyed () {
        this.removeInstallListener()
        window.removeEventListener('transmitDeleteWatchFaceResult', this.transmitDeleteWatchFaceResult, true)
    },
    computed: {
        ...mapGetters([
            'isMiddleEastLanguage',
            'currentDownloadWatchFaceId',
            'apiSign',
            'defaultWatchId',
            'installedWatchIds',
            'isBigCD',
            'isShowTipsBar',
            'isDeviceConnected'
        ])
    }
}
</script>

<style lang="scss" type="stylesheet/scss">
  @import  '../../assets/css/marketBtnFontSize.scss';
    .downloadFrame {
        text-align: center;
    }

    .installCancel {
        position: absolute;
        width: 2.4rem;
        height: 2.4rem;
        z-index: 10;
    }

    .installCancelNoraml{
      right: calc(100% + 2.8rem);
    }

    .installCancelReversal{
      left: calc(100% + 2.8rem);
    }

    .installCancelBigCDNoraml{
      right: calc(100% + 6.8rem);
    }
    .installCancelBigCDReversal{
      left: calc(100% + 6.8rem);
    }
    .marketButton {
        bottom: 0.8rem;
        left: 4.175rem;
        position: absolute;
    }

    @media (max-width: 320px) {
      .marketButton {
        bottom: 0;
        left: 2.44rem;
        position: absolute;
      }
    }

    .detailButton {
        bottom: 1rem;
        position: absolute;
    }
    .buttonLeft{
      left: calc(50% - 10rem);
    }
    .buttonLeftBigCD{
      left: calc(50% - 20rem);
    }
    .detailButton-download {
        bottom: 1rem;
        position: absolute;
        border: solid 0.1rem #fb6522;
        box-sizing: border-box;
        background: #fb6522;
        border-radius: 0.4rem;
        color: #ffffff;
    }

    .detailButton-downloading {
        bottom: 1rem;
        position: absolute;
        background: #fff;
        border-radius: 0.4rem;
    }

    .gray-light-border{
      border: 0.1rem solid rgba(194, 194,194, 0.3);
      box-sizing: border-box;
      border-radius: 0.4rem;
    }
    .gray-border {
        border: 0.1rem solid #c2c2c2;
        box-sizing: border-box;
        border-radius: 0.4rem;
    }

    .downloading-border {
        color: #1a1a1a;
        border: solid 0.1rem rgba(251, 101, 34, 0.2);
        box-sizing: border-box;
        border-radius: 0.4rem;
    }

    .downloadingStyle {
        height: 2.8rem;
        line-height: 2.8rem;
        width: 7.2rem;
        text-align: center;
        margin: 5rem auto 0 auto;
        border: solid 0.1rem #FB6522;
        box-sizing: border-box;
        opacity: 0.2;
        border-radius: 0.4rem;
        color: #1A1A1A;
        background: #ffffff;
    }

    .downloaded {
        height: 2.8rem;
        width: 7.2rem;
        line-height: 2.8rem;
        color: #1A1A1A;
        margin: 5rem auto 0 auto;
        text-align: center;
    }

    .defaultLightColor{
      color:rgba(26,26,26,0.3);
    }

    .downLoad {
        height: 2.8rem;
        line-height: 2.8rem;
        width: 7.2rem;
        color: #ffffff;
        text-align: center;
        margin: 5rem auto 0 auto;
        background: #fb6522;
        border: 0.1rem solid #fb6522;
        box-sizing: border-box;
        opacity: 0.2;
        border-radius: 0.4rem;
        overflow: hidden;
        text-overflow:ellipsis;
        white-space: nowrap;
    }

    .marketButtonColor {
        background: #fff;
        color: #1A1A1A;
        border: 0.1rem solid #c2c2c2;
        box-sizing: border-box;
        border-radius: 0.4rem;
    }

    .progressLeft{
      left:0;
    }

    .progressRight{
      right:0;
    }

    .progress {
        position: absolute;
        top: 0;
        height: 2.6rem;
        background: #fb6522;
        opacity: 0.2;
        z-index: 10;
        border-radius: 0.3rem;
        overflow: hidden;
    }

    .textContent {
        z-index: 11;
        position: relative;
        overflow: hidden;
        text-overflow:ellipsis;
        white-space: nowrap;
    }
  .bottomBtnStyle{
     line-height: 2.8rem;
     height:2.8rem;
     fontSize:0
  }
</style>



