export default {
    setPercentComplete (state, value) {
        state.percentComplete = value
    },
    setCurrentDownloadWatchFaceId (state, value) {
        state.currentDownloadWatchFaceId = value
    },
    setMiddleEastLanguage (state, value) {
        state.isMiddleEastLanguage = value
    },
    setApiSign (state, value) {
        state.apiSign = value
    },
    setMainTabIndex (state, value) {
        state.mainTabIndex = value
    },
    setScrollRecommend (state, value) {
        state.scrollRecommend = value
    },
    setScrollMyDial (state, value) {
        state.scrollMyDial = value
    },
    setDefaultWatchId (state, value) {
        state.defaultWatchId = value
    },
    setWatchInfoChanged (state, value) {
        state.watchInfoChanged = value
    },
    setWatchList (state, value) {
        state.watchList = value
    },
    setInstalledWatchIds (state, value) {
        state.installedWatchIds = value
    },
    setBigCD (state, value) {
        state.isBigCD = value
    },
    setTipsBarStatus (state, value) {
        state.isShowTipsBar = value.isShowTipsBar
        state.tipsText = value.tipsText
        state.isDeviceConnected = value.isDeviceConnected
    },
    setTipsBarHeight (state, value) {
        state.tipsHeight = value
    }
}



