<template>
  <div class="tipBar" v-show="isShowTipsBar" id="tipBarId">
    <img class='cautionIcon' src="../../assets/img/ic_caution.png">
    <p class="font_15 tipTextStyle">{{tipsText?tipsText :text}}</p>
  </div>
</template>

<script>
import {mapGetters} from 'vuex'
import log from '../../assets/js/log'
export default {
    name: 'noDeviceTipBar',
    computed: {
        ...mapGetters([
            'tipsText',
            'isShowTipsBar',
            'tipsHeight'
        ])
    },
    data () {
        return {
            text: this.tipsText ? this.tipsText : ''
        }
    },
    methods: {
        setTipsBarStatusChanged (event) {
            var data = event.data
            log.info('setTipsBarStatusChanged result# ' + data.isDeviceConnected + ', ' + data.tipsText)
            this.text = data.tipsText
            if (!data.isDeviceConnected) {
                this.$store.dispatch('setTipsBarStatus', {isShowTipsBar: true, tipsText: data.tipsText, isDeviceConnected: data.isDeviceConnected})
            } else {
                this.$store.dispatch('setTipsBarStatus', {isShowTipsBar: false, tipsText: '', isDeviceConnected: data.isDeviceConnected})
            }
        },
        setTipsBarHeight () {
            if (!this.tipsHeight) {
                var tipsBarHeight = document.getElementById('tipBarId').clientHeight
                log.info('setTipsHeight times tipsBar' + tipsBarHeight + 'px')
                this.$store.dispatch('setTipsBarHeight', tipsBarHeight + 'px')
            }
        }
    },
    mounted () {
        log.info('mounted tipsBar')
        log.info('index setTipsBarStatusChanged index# ')
        window.addEventListener('setTipsBarStatusChanged', this.setTipsBarStatusChanged, true)
    },
    updated () {
        log.info('updated tipsbar')
        if (this.text) {
            this.setTipsBarHeight()
        }
    },
    destroyed () {
        log.info('destroyed tipsbar')
        window.removeEventListener('setTipsBarStatusChanged', this.setTipsBarStatusChanged, true)
    }
}
</script>

<style lang="scss" type='stylesheet/scss'>
  .tipBar{
    width:calc(100% - 3.2rem);
    background-color: #ffe5e5;
    display: flex;
    flex-direction: row;
    align-items: center;
    padding-left: 1.6rem;
    padding-right: 1.6rem;
    padding-top: 0.8rem;
    padding-bottom:  0.8rem;
    position: fixed;
    z-index: 10000;
  }
  .cautionIcon{
    width:2.4rem;
    height:2.4rem;
  }
  .tipTextStyle{
    color:#ff3320;
    margin-left: 1.6rem;
  }
</style>



