<template>
    <div class='index'>
         <div class='titleBar font_16' id="tagTitle" >
           <!--eslint-disable-line-->
           <span v-for='(item,index) in tablist'  :id="titleBar(index)"  class='font_15' @click='changeindex(index)' v-bind:class='{active:tabindex===index,normal: !isMiddleEastLanguage,reversal: isMiddleEastLanguage}' :key='item.id'>{{item}}</span>
         </div>
        <div class='tablist'>
      <v-tipsBar class="tipBarTop"/>
      <div class='tablist' :style="{marginTop:!isShowTipsBar ? '0rem' : tipsHeight}">
          <swiper :options="swiperOption" ref="swiperView"  :style="{height:wClientHeight}">
            <swiper-slide>
                <div class='scrollContent' id='recommend'>
                   <v-recommend  ref='recommend' :dataAllList ='recommendList' :fileHost='fileHost'></v-recommend>
                </div>
            </swiper-slide>
            <swiper-slide >
                <div class='scrollContent' id='myDial'>
                    <v-myDial  ref='myDial' :dataMyList ='dataMyList' :fileHost='fileHost'></v-myDial>
                </div>
            </swiper-slide>
          </swiper>
        </div>
    </div>
    </div>
</template>

<script>
import 'swiper/dist/css/swiper.css'
import { swiper, swiperSlide } from 'vue-awesome-swiper'
import log from '../assets/js/log.js'
import config from '../assets/js/config.js'
import jsInjection from '../assets/js/jsInjectionj.js'
import CloudRequest from '../assets/js/otherCloudReq.js'
import setBiEvent from '../assets/js/setBiEvent.js'
import recommend from '@/components/recommend'
import myDial from '@/components/myDial'
import tipsBar from '@/components/common/tipsBar'
import {mapGetters} from 'vuex'
export default {
    name: 'index',
    inject: ['reload'],
    computed: {
        ...mapGetters([
            'isMiddleEastLanguage',
            'mainTabIndex',
            'watchInfoChanged',
            'scrollRecommend',
            'scrollMyDial',
            'isShowTipsBar',
            'tipsHeight',
            'isDeviceConnected'
        ])
    },
    data () {
        const that = this
        return {
            tablist: [this.$t('IDS_watchface_recommend'), this.$t('IDS_watchface_mine')],
            tabindex: 0,
            dataAllList: [],
            recommendList: [],
            dataMyList: [],
            dataMineList: [],
            fileHost: '',
            currentWatchFaceId: '',
            watchFaceIdStore: [],
            preWatchFaceIdStore: [],
            presetWatchIds: [],
            installedWatchIds: [],
            installedWatchVersions: [],
            sign: '',
            pageCount: 1,
            totalPage: 1,
            requestMoreEnable: false,
            newstr: '',
            unfoundedWatches: [], // 未在云端找到的表盘id,通过native从手表获取表盘名称ID
            unfoundedWatchesNames: [], // 未在云端找到的表盘名称,通过native从手表获取表盘名称
            unfoundedWatchesBrief: [], // 通过native从手表获取表盘简介
            unfoundedWatchesSize: [], // 通过native从手表获取表盘图片
            unfoundedWatchesUri: [], // 通过native从手表获取表盘图片
            unfoundedWatchesNum: [], // 通过native从第一个还是第二个
            watchThemelength: config.WATCH_THEME_DEFAULT_LENGTH, // 页面显示表盘个数
            recordCount: 0,
            wClientHeight: '52rem',
            swiperOption: {
                autoHeight: false,
                resistanceRatio: 0,
                on: {
                    slideChange: function () {
                        that.tabindex = this.activeIndex
                        that.$store.dispatch('setMainTabIndex', that.tabindex)
                        log.info('slideChange: ' + that.tabindex)
                        that.calculateHeight()
                    }
                }
            }
        }
    },
    methods: {
        startScroll () {
            document.getElementById('recommend').addEventListener('scroll', () => {
                var recommendScroll = document.getElementById('recommend').scrollTop
                this.$store.commit('setScrollRecommend', recommendScroll)
            })
            document.getElementById('myDial').addEventListener('scroll', () => {
                var myDialScroll = document.getElementById('myDial').scrollTop
                this.$store.commit('setScrollMyDial', myDialScroll)
            })
        },
        calculateHeight () {
            // 获取当前可视化窗口的高度
            var tagHeight = document.getElementById('tagTitle').clientHeight
            var cliHeight = document.documentElement.clientHeight
            this.wClientHeight = (cliHeight - tagHeight) + 'px'
        },
        transmitWatchInfoChange () {
            // 设备侧更改状态刷新页面
            log.info('transmitWatchInfoChange')
            this.filteringData()
        },
        changeindex (index) {
            this.tabindex = index
            this.$refs.swiperView.swiper.slideTo(index, 400, false)
            this.$store.dispatch('setMainTabIndex', index)
            log.info('isDeviceConnected changeindex# ' + index)
            if (this.isDeviceConnected) {
                log.info('isDeviceConnected ' + this.isDeviceConnected + 'filteringData*****')
                this.filteringData(true)
            }
        },
        loadMyPage () {
            log.info('load my page: ' + this.recordCount)
            var params = {
                sign: this.sign,
                length: this.recordCount
            }
            CloudRequest.request('queryAllDial', params, data => {
                if (!data.list) {
                    return
                }
                this.dataMineList.push.apply(this.dataMineList, data.list)
                // this.dataMineList = data.list
                this.dataAllList.length = 0
                this.dataAllList = Array.from(data.list)
                this.addField()
                this.$store.dispatch('setWatchList', data.list)
                this.addMyField(data.list)
                // 检查是否有未在云端查到的表盘
                this.checkUnFoundedWatches()
                // 显示表盘市场成功bi打点
                setBiEvent.clickWatchFace(1)
            }, error => {
                log.info('请求失败: ' + error)
                // 显示表盘市场失败bi打点
                setBiEvent.clickWatchFace(0)
                jsInjection.sendServerErrMsg()
            })
        },
        addField () {
            this.loadWatchInfoFromNative()
            this.recommendList.length = 0
            let currentWatchId = this.currentWatchFaceId.split('_')[0]
            this.$store.dispatch('setDefaultWatchId', currentWatchId)
            for (var p in this.dataAllList) {
                this.newstr = this.dataAllList[p].hitopid + '_' + this.dataAllList[p].version
                this.dataAllList[p].watchThemeModle = config.WATCH_THEME_TYPE_DOWNLOAD_NO
                this.dataAllList[p].isPresetWatch = false
                // 预置表盘不在推荐页面显示
                if (this.isPresetWatch(this.dataAllList[p].hitopid)) {
                    continue
                }
                if (currentWatchId === this.dataAllList[p].hitopid) {
                    var newWatchInfo = Object.assign({}, this.dataAllList[p])
                    if (this.newstr === this.currentWatchFaceId) {
                        newWatchInfo.watchThemeModle = config.WATCH_THEME_TYPE_DEFAULT
                    } else {
                        newWatchInfo.watchThemeModle = config.WATCH_THEME_TYPE_DOWNLOAD_NO
                    }
                    // 新增预置表盘第3位版本号大于9000,推荐页面不显示（新增预置表盘不能下载，因此不能在表盘市场推荐页面显示，又需要在表盘市场我的页面显示，需在主题云上传生成id，拷到手表侧）
                    if (Number(newWatchInfo.version.split('.')[2]) < config.NEW_ADD_PRESET_WATCH_FACE_VERSION) {
                        this.recommendList.push(newWatchInfo)
                    }
                    log.info(newWatchInfo.hitopid + ', model2 = ' + newWatchInfo.watchThemeModle)
                    continue
                } else if (this.watchFaceIdStore.indexOf(this.newstr) > -1) {
                    this.dataAllList[p].watchThemeModle = config.WATCH_THEME_TYPE_ADD
                }
                log.info(this.dataAllList[p].hitopid + ', model = ' + this.dataAllList[p].watchThemeModle)
                if (Number(this.dataAllList[p].version.split('.')[2]) < config.NEW_ADD_PRESET_WATCH_FACE_VERSION) {
                    this.recommendList.push(this.dataAllList[p])
                }
            }
            log.info('addField:' + this.dataAllList.length + ' - ' + this.recommendList.length)
        },
        isPresetWatch (hitopid) {
            return this.presetWatchIds.indexOf(hitopid) > -1
        },
        loadWatchInfoFromNative () {
            this.currentWatchFaceId = jsInjection.get('currentWatchFaceId')
            this.watchFaceIdStore = jsInjection.get('watchFaceIdStore').split(',')
            this.preWatchFaceIdStore = jsInjection.get('preWatchFaceIdStore').split(',')
            this.presetWatchIds = this.preWatchFaceIdStore.map((idVersion) => {
                return idVersion.split('_')[0]
            })
            this.installedWatchIds = this.watchFaceIdStore.map((idVersion) => {
                return idVersion.split('_')[0]
            })
            this.installedWatchVersions.length = 0
            this.$store.commit('setInstalledWatchIds', this.installedWatchIds)
            // 用户点击下载后退出表盘市场页面,直到安装成功后也未再进入页面,需要清空在此之前保存的安装进度
            this.watchFaceIdStore.forEach((value, index, arr) => {
                let idVersion = value.split('_')
                this.installedWatchVersions.push(idVersion[1])
                localStorage.removeItem(value)
                if (value === localStorage.getItem(config.CURRENT_DOWNLOADING_WATCH_ID)) {
                    log.info('clearDownloadingWatchFaceId' + value)
                    localStorage.removeItem(config.CURRENT_DOWNLOADING_WATCH_ID)
                }
            })
            log.info('currentWatchFaceId: ' + this.currentWatchFaceId + ' watchFaceIdStore: ' + this.watchFaceIdStore)
            log.info('preWatchFaceIdStore: ' + this.preWatchFaceIdStore)
        },
        addMyField (list) {
            this.loadWatchInfoFromNative()
            let currentWatchId = this.currentWatchFaceId.split('_')[0]
            this.$store.dispatch('setDefaultWatchId', currentWatchId)
            for (var p in list) {
                this.newstr = list[p].hitopid + '_' + list[p].version
                list[p].watchThemeModle = config.WATCH_THEME_TYPE_DOWNLOAD_NO
                list[p].isPresetWatch = false
                if (currentWatchId === list[p].hitopid) {
                    list[p].watchThemeModle = config.WATCH_THEME_TYPE_DEFAULT
                    this.dataMyList.push(list[p])
                    list[p].isPresetWatch = this.isPresetWatch(list[p].hitopid)
                } else if (this.watchFaceIdStore.indexOf(this.newstr) > -1) {
                    // 用户安装表盘：id和version匹配
                    list[p].watchThemeModle = config.WATCH_THEME_TYPE_ADD
                    this.dataMyList.push(list[p])
                } else if (this.installedWatchIds.indexOf(list[p].hitopid) > -1) {
                    // 用户安装表盘：云端和手表侧version不同
                    var newWatch = Object.assign({}, list[p])
                    newWatch.watchThemeModle = config.WATCH_THEME_TYPE_ADD
                    this.dataMyList.push(newWatch)
                } else {
                    if (this.isPresetWatch(list[p].hitopid)) {
                        list[p].watchThemeModle = config.WATCH_THEME_TYPE_REESET
                        list[p].isPresetWatch = true
                        this.dataMyList.push(list[p])
                    }
                }
            }
            log.info('dataMyList' + this.dataMyList.length)
        },
        // 小语种判断
        judgeLang: function () {
            let ArabicLanguage = ['ar_SA', 'he_IL', 'iw_IL', 'fa_IR', 'ur_PK', 'ug_CN', 'ar', 'he', 'iw', 'fa', 'ur', 'ug'] // he_IL和iw_IL兼容之前的
            let locale = jsInjection.get('locale')
            let preLocale = jsInjection.get('locale').split('_')[0]
            log.info('judgeLang# locale =' + locale + ',preLocale = ' + preLocale)
            ArabicLanguage.forEach(item => {
                if (item === locale || item === preLocale) {
                    this.$store.dispatch('setMiddleEastLanguage', true)
                }
            })
        },
        filteringData: function (event) {
            log.debug('Index#watchInfoChanged = ' + this.watchInfoChanged)
            this.addField()
            if (this.watchInfoChanged || event) {
                this.$store.dispatch('setWatchInfoChanged', false)
            }
            if (this.dataMineList.length > 0) {
                this.dataMyList.splice(0, this.dataMyList.length)
                this.addMyField(this.dataMineList)
                this.addUnfoundedWatchesIfNeeded()
            }
        },
        // 添加未在云端找到的表盘到我的页面。
        addUnfoundedWatchesIfNeeded () {
            log.info('unfoundedWatches: ' + JSON.stringify(this.unfoundedWatches))
            for (var p in this.unfoundedWatches) {
                let hitopid = this.unfoundedWatches[p]
                let watchName = this.unfoundedWatchesNames[p]
                let index = this.installedWatchIds.indexOf(hitopid)
                let watchBrief = this.unfoundedWatchesBrief[p]
                let watchSize = this.unfoundedWatchesSize[p]
                let watchUri = this.unfoundedWatchesUri[p]
                let pushNum = this.unfoundedWatchesNum[p]
                let watch = {
                    hitopid: hitopid,
                    briefinfo: watchBrief,
                    'title-local': watchName,
                    name: watchName,
                    previews: watchUri,
                    size: watchSize,
                    downloadcount: 0,
                    version: this.installedWatchVersions[index],
                    pushNum: pushNum
                }
                log.info('addUnfoundedWatchesIfNeeded: ' + hitopid + ' = ' + watchName)
                watch.isPresetWatch = false
                var model = config.WATCH_THEME_TYPE_DOWNLOAD_NO
                if (this.presetWatchIds.indexOf(hitopid) >= 0) {
                    log.info('hitopid = ' + hitopid + ', is preset')
                    model = config.WATCH_THEME_TYPE_REESET
                    watch.isPresetWatch = true
                } else if (this.installedWatchIds.indexOf(hitopid) >= 0) {
                    log.info('hitopid = ' + hitopid + ', is installed')
                    model = config.WATCH_THEME_TYPE_ADD
                }
                let currentWatchId = this.currentWatchFaceId.split('_')[0]
                this.$store.dispatch('setDefaultWatchId', currentWatchId)
                if (currentWatchId === hitopid) {
                    model = config.WATCH_THEME_TYPE_DEFAULT
                    log.info('hitopid = ' + hitopid + ', is default')
                }
                watch.watchThemeModle = model
                this.dataMyList.push(watch)
                this.dataMineList.push(watch)
            }
            this.clearUnFoundedWatches()
            log.info('addUnfoundedWatchesIfNeeded: ' + this.dataMyList.length)
        },
        // native通知h5，从手表获取到了表盘名称。
        transmitWatchFaceNames (names) {
            log.info('transmitWatchFaceNames: ' + names)
            if (names.length === 0) {
                return
            }
            this.clearUnFoundedWatches()
            names.split(',').forEach((value, index, array) => {
                let idNames = value.split('_')
                if (idNames.length === config.WATCH_GT && idNames[config.WATCH_THEME_ARRAY_ID_ZORE].length > 0 && idNames[config.WATCH_THEME_ARRAY_NAME_ONE].length > 0) {
                    log.info('transmitWatchFaceNames_WATCH_IS_GT:' + idNames)
                    this.unfoundedWatches.push(idNames[config.WATCH_THEME_ARRAY_ID_ZORE])
                    this.unfoundedWatchesNames.push(idNames[config.WATCH_THEME_ARRAY_NAME_ONE])
                    this.unfoundedWatchesBrief.push(idNames[config.WATCH_THEME_ARRAY_NAME_ONE])
                    this.unfoundedWatchesSize.push('0')
                    this.unfoundedWatchesUri.push('')
                    this.unfoundedWatchesNum.push(config.WATCH_GT)
                }
                if (idNames.length === config.WATCH_CASSINI && idNames[config.WATCH_THEME_ARRAY_ID_ZORE].length > 0 && idNames[config.WATCH_THEME_ARRAY_NAME_ONE].length > 0) {
                    log.info('transmitWatchFaceNames_WATCH_IS_CASSINI:' + idNames)
                    this.unfoundedWatches.push(idNames[config.WATCH_THEME_ARRAY_ID_ZORE])
                    this.unfoundedWatchesNames.push(idNames[config.WATCH_THEME_ARRAY_NAME_ONE])
                    this.unfoundedWatchesBrief.push(idNames[config.WATCH_THEME_ARRAY_BRIEF_TWO])
                    this.unfoundedWatchesSize.push(idNames[config.WATCH_THEME_ARRAY_SIZE_THREE])
                    this.unfoundedWatchesUri.push(idNames[config.WATCH_THEME_ARRAY_URI_FOUR])
                    this.unfoundedWatchesNum.push(config.WATCH_CASSINI)
                }
            })
            this.addUnfoundedWatchesIfNeeded()
        },
        // 清除未检测云盘查询的表盘
        clearUnFoundedWatches () {
            this.unfoundedWatches.length = 0
            this.unfoundedWatchesNames.length = 0
            this.unfoundedWatchesBrief.length = 0
            this.unfoundedWatchesSize.length = 0
            this.unfoundedWatchesUri.length = 0
            this.unfoundedWatchesNum.length = 0
        },
        // 检索未在云端查询到的表盘id
        checkUnFoundedWatches () {
            // 从云端查询到的所有表盘Id
            let watchIds = this.dataAllList.map((watch, index, array) => {
                return watch.hitopid
            })
            // 在云端未查找到的表盘id列表
            var unFoundedList = []
            let filter = (hitopid, index, array) => {
                if (hitopid.length > 0 && watchIds.indexOf(hitopid) < 0) {
                    unFoundedList.push(hitopid)
                }
            }
            this.presetWatchIds.forEach(filter)
            this.installedWatchIds.forEach(filter)
            log.info('unFoundedList:' + unFoundedList)
            if (unFoundedList.length > 0) {
                // 从native获取表盘名称
                jsInjection.getWatchFaceNames(unFoundedList.join(','))
            }
        },
        titleBar: function (index) {
            return 'titleBar' + index
        },
        initTitleWidth: function () {
            var recommendWidth = document.getElementById('titleBar0').offsetWidth
            var mineWidth = document.getElementById('titleBar1').offsetWidth
            if (recommendWidth > mineWidth) {
                document.getElementById('titleBar1').style = 'box-sizing:border-box;width:' + recommendWidth + 'px'
            } else if (recommendWidth < mineWidth) {
                document.getElementById('titleBar0').style = 'box-sizing:border-box;width:' + mineWidth + 'px'
            }
        },
        isBigCD () {
            let isBigCDValue = jsInjection.get('getBigCDStatus')
            log.info('isBigCD index# ' + isBigCDValue + ', ' + typeof isBigCDValue)
            this.$store.dispatch('setBigCD', isBigCDValue)
        }
    },
    created () {
        log.info('Index#created: ' + window.devicePixelRatio)
        // 手表测更改状态
        window.transmitWatchInfoChange = this.transmitWatchInfoChange
        // 判断是否是中东语言
        this.judgeLang()
        // 是否是大溪地手机
        this.isBigCD()
        // 发送请求获取客户端前置接口
        jsInjection.setTitle(this.$t('IDS_watchface_watchFaceMacket'))
        CloudRequest.request('getSize', {}, data => {
            // 发送请求获取所有的表盘
            log.debug('data.body.sign: ' + data.sign)
            this.sign = data.sign
            this.$store.dispatch('setApiSign', data.sign)
            var params = {
                sign: this.sign,
                begin: this.pageCount,
                length: this.watchThemelength
            }
            CloudRequest.request('queryAllDial', params, data => {
                if (!data.list) {
                    return
                }
                this.recordCount = data.recordCount
                this.dataAllList = data.list
                this.fileHost = data.fileHost
                this.totalPage = data.totalPage
                this.requestMoreEnable = true
                // 给返回数据里面添加属性  判断是否是默认  是否是下载
                this.addField()
                // 我的页面所需的数据
                this.loadMyPage()
            }, error => {
                log.info('请求失败: ' + error)
                jsInjection.sendServerErrMsg()
            })
        }, error => {
            log.info('请求失败 : ' + error)
            jsInjection.sendServerErrMsg()
        })
    },
    mounted () {
        log.info('Index#mounted')
        window.transmitWatchInfoChange = this.transmitWatchInfoChange
        window.transmitWatchFaceNames = this.transmitWatchFaceNames
        this.filteringData()
        window.addEventListener('defaultWatchChanged', this.filteringData, true)
        this.initTitleWidth()
        this.calculateHeight()
        this.startScroll()
    },
    activated () {
        document.getElementById('recommend').scrollTop = this.scrollRecommend
        document.getElementById('myDial').scrollTop = this.scrollMyDial
        log.info('Index#activated')
        jsInjection.setTitle(this.$t('IDS_watchface_watchFaceMacket'))
        this.tabindex = this.mainTabIndex
        window.transmitWatchInfoChange = this.transmitWatchInfoChange
        window.transmitWatchFaceNames = this.transmitWatchFaceNames
        this.filteringData()
        window.addEventListener('defaultWatchChanged', this.filteringData, true)
    },
    deactivated () {
        log.info('Index#deactivated')
        window.removeEventListener('defaultWatchChanged', this.filteringData, true)
    },
    components: {
        'v-recommend': recommend,
        'v-myDial': myDial,
        swiper,
        swiperSlide,
        'v-tipsBar': tipsBar
    }
}
</script>

<!-- Add 'scoped' attribute to limit CSS to this component only -->
<style lang='scss' type='stylesheet/scss'>
.scrollContent{
    position: absolute;
    top:0;
    bottom:0;
    overflow-y: scroll;
    word-wrap: break-word;
    -webkit-overflow-scrolling : touch;
}
.scrollContent::-webkit-scrollbar {
  display:none;
}
.index{
    display: flex;
    flex-direction: column;
    margin: 0;
    padding: 0;
    .titleBar{
      height: 4.8rem;
      display: flex;
      justify-content: space-between;
      align-items: center;
      position: fixed;
      width: 100%;
      background-color:#f2f2f2;
      z-index:10000;
    span {
      display: inline-block;
      width:auto;
      max-width: 14.4rem;
      border-bottom: 0.2rem solid #f2f2f2;
      color: #1a1a1a;
      opacity: 0.5;
      text-align: center;
      padding: 0.4rem 0.8rem;
      overflow: hidden;
      text-overflow:ellipsis;
      white-space: nowrap;
    }
    .normal:nth-child(odd){
      position: absolute;
      bottom:0.8rem;
      right: 50%;
      transform: translateX(-0.4rem);
    }
    .normal:nth-child(even){
      position: absolute;
      bottom:0.8rem;
      left: 50%;
      transform: translateX(0.4rem);
    }
    .reversal:nth-child(odd){
      position: absolute;
      left: 50%;
      bottom:0.8rem;
      transform: translateX(0.4rem);
    }
    .reversal:nth-child(even){
      position: absolute;
      bottom:0.8rem;
      right: 50%;
      transform: translateX(-0.4rem);
    }
    span.active {
      opacity: 1;
      color: #FB6522;
      border-bottom-color: #FB6522;
    }
  }
  .tipBarTop{
      top:4.8rem;
  }
}
.tablist{
    margin-top:4.8rem;
}
</style>



